<template>
	<div class="layout">
		<Header class="header">中商产品
		<span style="float: right;" @cl>{{site}}</span></Header>
		<Content class="content top_p bottom_p">
			<div class="hm_product_num">
				<Tabs value="name1">
					<TabPane label="商标" name="name1" span="8">
						<Row type="flex" :gutter="16"> 
							<col span="8">
							<p  v-for="(item, name, index) in sb_fl" @click="product_list(item.categoryFir)"><img :src=item.src />{{item.name}}</p>
							</col> 
							<col span="8">
							<p @click="sb_all"><img src="../../assets/sb_all.png"/>查看全部</p>
							</col>
						</Row>
					</TabPane>
					<TabPane label="国际商标" name="name2"> 
						<Row type="flex" :gutter="16">
							<div></div>
							<col span="8">
							<p  v-for="(item, name, index) in gjsb" @click="gj_product_list(item.areaCode)"><img :src=item.src />{{item.name}}</p>
							</col>
						</Row></TabPane>
					<TabPane label="名标" name="name3"> 
						<Row type="flex" :gutter="16">
							<div></div>
							<col span="8">
							<p  v-for="(item, name, index) in mb" @click="mb_product_list(item.categoryFir)"><img :src=item.src />{{item.name}}</p>
							</col>
							<col span="8">
							<p @click="sb_all_mb()"><img src="../../assets/sb_all.png"/>查看全部</p>
							</col>
						</Row></TabPane>
					<TabPane label="专利" name="name4"> 
						<Row type="flex" :gutter="16">
							<div></div>
							<col span="8">
							<p  v-for="(item, name, index) in zl_fl" @click="zl_meau(item.categoryFir,item.value)"><img :src=item.src />{{item.name}}</p>
							</col>
						</Row>
					</TabPane>
				</Tabs>
			</div>
		</Content>
		<Footer class="footer">
			<Row type="flex">
				<col span="6">
				<router-link to="/"><img src="../../assets/icon_1.png" /><br />首页</router-link>
				</col>
				<col span="6">
				<router-link to="product" class="active"><img src="../../assets/icon_2_hover.png" /><br />产品</router-link>
				</col>
				<col span="6">
				<router-link to="service"><img src="../../assets/icon_3.png" /><br />分类</router-link>
				</col>
				<col span="6">
				<a @click="myvip"><img src="../../assets/icon_4.png" /><br />我的</a>
				</col>
			</Row>
		</Footer>
	</div>
</template>

<script>
	export default {
		name: 'product_list',
		data() {
			return { 
				site:this.$route.query.site,
				sb_fl: [{
						categoryFir: '01类-化学原料',
						name: '01类-化学原料',
						src: require('../../assets/sb_1.png'),
					},
					{
						categoryFir: '02类-涂料油漆',
						name: '02类-涂料油漆',
						src: require('../../assets/sb_2.png'),
					},
					{
						categoryFir: '03类-日化用品',
						name: '03类-日化用品',
						src: require('../../assets/sb_3.png'),
					},
					{
						categoryFir: '04类-燃料油脂',
						name: '04类-燃料油脂',
						src: require('../../assets/sb_4.png'),
					},
					{
						categoryFir: '05类-医药品',
						name: '05类-医药品',
						src: require('../../assets/sb_5.png'),
					},
					{
						categoryFir: '06类-五金金属',
						name: '06类-五金金属',
						src: require('../../assets/sb_6.png'),
					},
					{
						categoryFir: '07类-机械设备',
						name: '07类-机械设备',
						src: require('../../assets/sb_7.png'),
					},
					{
						categoryFir: '08类-手工器械',
						name: '08类-手工器械',
						src: require('../../assets/sb_8.png'),
					}
				],
				gjsb: [
				{
						areaCode: '美国商标',
						name: '美国商标',
						src: require('../../assets/meiguo.png'),
					},{
						areaCode: '欧盟商标',
						name: '欧盟商标',
						src: require('../../assets/oumeng.png'),
					},
					{
						areaCode: '德国商标',
						name: '德国商标',
						src: require('../../assets/deguo.png'),
					},
					{
						areaCode: '法国商标',
						name: '法国商标',
						src: require('../../assets/faguo.png'),
					},
					{
						areaCode: '香港商标',
						name: '香港商标',
						src: require('../../assets/xiangg.png'),
					},
					{
						areaCode: '台湾商标',
						name: '台湾商标',
						src: require('../../assets/taiwn.png'),
					},
					{
						areaCode: '韩国商标',
						name: '韩国商标',
						src: require('../../assets/hanguo.png'),
					},
					{
						areaCode: '日本商标',
						name: '日本商标',
						src: require('../../assets/riben.png'),
					},
					{
						areaCode: '其他国家',
						name: '其他国家',
						src: require('../../assets/othercon.png'),
					}
				],
				mb: [{
						categoryFir: '01类-化学原料',
						name: '01类-化学原料',
						src: require('../../assets/sb_1.png'),
					},
					{
						categoryFir: '02类-涂料油漆',
						name: '02类-涂料油漆',
						src: require('../../assets/sb_2.png'),
					},
					{
						categoryFir: '03类-日化用品',
						name: '03类-日化用品',
						src: require('../../assets/sb_3.png'),
					},
					{
						categoryFir: '04类-燃料油脂',
						name: '04类-燃料油脂',
						src: require('../../assets/sb_4.png'),
					},
					{
						categoryFir: '05类-医药品',
						name: '05类-医药品',
						src: require('../../assets/sb_5.png'),
					},
					{
						categoryFir: '06类-五金金属',
						name: '06类-五金金属',
						src: require('../../assets/sb_6.png'),
					},
					{
						categoryFir: '07类-机械设备',
						name: '07类-机械设备',
						src: require('../../assets/sb_7.png'),
					},
					{
						categoryFir: '08类-手工器械',
						name: '08类-手工器械',
						src: require('../../assets/sb_8.png'),
					}
				],
				zl_fl: [
				{
						categoryFir: '生活需要',
						value:'001',
						name: '生活需要',
						src: require('../../assets/shxy.png'),
					},
					{
						categoryFir: '作业运输',
						value:'002',
						name: '作业运输',
						src: require('../../assets/zyys.png'),
					},
					{
						categoryFir: '化学冶金',
						value:'003',
						name: '化学冶金',
						src: require('../../assets/hxyj.png'),
					},
					{
						categoryFir: '纺织造纸',
						value:'004',
						name: '纺织造纸.',
						src: require('../../assets/fzzz.png'),
					},
					{
						categoryFir: '固定建筑',
						value:'005',
						name: '固定建筑',
						src: require('../../assets/gdjz.png'),
					},
					{
						categoryFir: '机械工程',
						value:'006',
						name: '机械工程',
						src: require('../../assets/jxgc.png'),
					},
					{
						categoryFir: '物理',
						value:'007',
						name: '物理',
						src: require('../../assets/wul.png'),
					},
					{
						categoryFir: '电学',
						value:'008',
						name: '电学',
						src: require('../../assets/dianx.png'),
					},
				]
			}
		},
		components: {

		},
		methods: { 
			
      open() {
        const h = this.$createElement;
        this.$msgbox({
          title: '消息',
          message: h('p', null, [
            h('span', null, '内容可以是 '),
            h('i', { style: 'color: teal' }, 'VNode')
          ]),
          showCancelButton: true,
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          beforeClose: (action, instance, done) => {
            if (action === 'confirm') {
              instance.confirmButtonLoading = true;
              instance.confirmButtonText = '执行中...';
              setTimeout(() => {
                done();
                setTimeout(() => {
                  instance.confirmButtonLoading = false;
                }, 300);
              }, 3000);
            } else {
              done();
            }
          }
        }).then(action => {
          this.$message({
            type: 'info',
            message: 'action: ' + action
          });
        });
      },
			product_list(categoryFir) {
				if (categoryFir !='all') {
				this.$router.push({
					path: '/sb_list', 
                    query:{  
                    categoryFir:categoryFir, // orderNum : this.searchData.orderNo
                    applyType:'1',
                    inlandFlag:'Y',
                    areaCode:this.$route.query.site,
                    } 
				}) 
				} else{
					this.$router.push({
					path: '/sb_all',  
				}) 
				}
			}, 
			
			mb_product_list(categoryFir) {
				if (categoryFir !='all') {
				this.$router.push({
					path: '/sb_list', 
                    query:{  
                    categoryFir:categoryFir,
                    vipFlag:'Y',
                    applyType:'1',
                    areaCode:this.$route.query.site,
                    } 
				}) 
				} else{
					this.$router.push({
					path: '/sb_all',  
                    query:{   
                    vipFlag:'Y',
                    areaCode:this.$route.query.site,
                    } 
				}) 
				}
			}, 
			
			gj_product_list(categoryFir) { 
				this.$router.push({
					path: '/sb_list', 
                    query:{  
                    categoryFir:categoryFir, // orderNum : this.searchData.orderNo
                    applyType:'1',
                    inlandFlag:'N',
                    areaCode:this.$route.query.site,
                    } 
				})  
			}, 
			zl_meau(categoryFir,value) {
				this.$router.push({
					path: '/zl_meau', 
                    query:{  
                    	categoryFir:categoryFir, 
                    applyType:'2',
                    value:value,
                    areaCode:this.$route.query.site,
                    } 
				})
			},
			
			sb_all() {
				this.$router.push({
					path: '/sb_all'
				})
			},
			sb_all_mb() {
				this.$router.push({
					path: '/sb_all',
					query:{ 
                    inlandFlag:'Y',
                    areaCode:this.$route.query.site,
                    } 
				})
			},
			myvip() {
				let loginName = sessionStorage.loginName
				if(loginName != null) {
					this.$router.push({
						path: '/my'
					})
				} else {
					this.$router.push({
						path: '/login'
					})
				}
			},
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;
		p {
			font-size: 14px;
			color: #000000;
			text-align: center;
			width: 33%;
			margin: 16px 0 26px 0;
			img {
				height: 76px;
				width: 76px;
				display: block;
				margin: 0 auto 5px auto;
			}
		}
	}
</style>


