<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>账户提现</Header>
		<Content class="content6 top_p">
			<div class="hm_product_num" v-model="product" >
				<Row :gutter="16" class="big_h">
					<Col span="24">
					<div class="my_list">
						<span class="left">
									<p class="title">金额</p></span>
						<input  v-model="product.amount" placeholder="请输入金额" />
					</div>
					</Col>
					<Col span="24">
					<div class="my_list">
						<span class="left">
									<p class="title">收款人</p></span>
						<input  v-model="loginName" placeholder="请输入收款人" />
					</div>
					</Col>
					<Col span="24">
					<div class="my_list">
						<span class="left">
									<p class="title">收款账户</p></span>
						<input  v-model="product.description" placeholder="请输入收款账户" />
					</div>
					</Col>
				</Row>
				<Button type="primary" shape="circle" @click="getProducts">确认提交</Button>
			</div>
		</Content>
	</div>
</template>
<script>
	import { applydoApply } from '../../api/api';
	export default {
		name: 'zhtx',
		data() {
			return {
				description: '',
				amount: '', 
				loginName:sessionStorage.loginName,
				product:[],
			}
		},
		components: {

		},
		methods: {
			onload() {
				if(sessionStorage.customerId == null) {
					this.$router.push({
						path: '/login'
					})
				}
			},
			prev() {
				this.$router.go(-1)
			},
			getProducts() {
				console.log(this.product.amount)
				if (this.product.amount !=='' && this.product.description !=='') {
					let para = {
					status: 0,
					applyType: 3,
					customerId: sessionStorage.customerId, 
					amount: this.product.amount,
					description: this.product.description,
					sallerMobile: sessionStorage.sallerMobile,
				};
				applydoApply(para).then((res) => {
					if(res.data.code == 0) {
						//console.log(res)
						this.$Message.success('提交成功');
						this.$router.push({
							path: '/txcg'
						})
					} else {
						this.$Message.warning(res.data.message);
					}
				})
				} else{
						this.$Message.warning('信息未提交成功～');
				}
				},
			cancel() {
				this.$Message.warning('提交失败');
			}
		},
		props: {},
		mounted() {
			this.onload();
		}
	}
</script>

<style lang="less" scoped>
	.bg_box {
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 152px;
		display: block;
		background-image: url('../../assets/ser_bg.png');
		font-size: 23px;
		color: #FFFFFF;
		text-align: center;
		line-height: 152px;
		font-weight: bolder;
	}
	
	.top {
		margin-top: 152px;
	}
	
	.big_h {
		margin-top: -15px;
		margin-bottom: 30px;
	}
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		.my_list {
			overflow: hidden;
			height: 60px;
			line-height: 60px;
			border-bottom: 1px solid #E6E6E6;
			display: flex;
			justify-content: space-between;
			.left {
				align-content: flex-start;
				.title {
					border-radius: 0 0 6px 6px;
					bottom: 0px;
					font-size: 16px;
					color: #000000;
					margin: 0;
					float: left;
					display: inline-block;
					width: 100px;
					text-align: left;
					align-content: flex-start;
				}
			}
			input {
				border: none;
				outline: none;
				font-size: 16px;
				display: inline-block;
				float: right;
				width: 260px;
				text-align: right;
			}
		}
		.line {
			width: 100%;
			background-color: #D7D7D7;
			margin-bottom: 20px;
			height: 1px;
			padding: 0;
		}
	}
</style>


