<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>意见反馈</Header>
		<div class="content6 top_p">
			<Form ref="formCustom" :data="formCustom" :model="formCustom" :rules="ruleCustom" class="big_h" :label-width="0">
				<FormItem class="my_list" prop="title">
					<span class="yjbk">
			 		<Input type="textarea"  v-model="formCustom.jsonStr" :autosize="{minRows: 5,maxRows: 8}" placeholder="请输入您的建议反馈..." />
</Input>
			 	</span></FormItem>
				<Button type="primary" shape="circle" @click="handleSubmit('formCustom')">立即发布</Button>
			</Form>
		</div>
	</div>
</template>

<script>
	import { Collectcreate } from '../../api/api';
	export default {
		name: 'wjmm',
		data() {
			return {
				formCustom: { 
					jsonStr: '',
				},
				ruleCustom: {
					jsonStr: [{
						required: true,
						message: ' ',
						trigger: 'blur'
					}]
				}
				}
		},
		components: {},
		props: {},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			handleSubmit(name) {
				this.$refs[name].validate((valid) => {
					if(valid) {
						let para = { 
							jsonStr: this.formCustom.jsonStr,
							customerId: sessionStorage.customerId,  
							actionType:'1',
							contentType: '3',
						};
						Collectcreate(para).then((res) => {
							//console.log(res.data)
							if(res.data.code == 0) {
								this.$Message.success('发布成功!');
								this.$router.push({
									path: '/my'
								})
							} else {
								this.$Message.warning(res.data.message);
							}
						});
					} else {
						this.$Message.error('输入信息有误');
					}
				})
			},

		},
	}
</script>

<style lang="less" scoped>
	.yjbk {
		border: 1px solid #E6E6E6;
		display: block;
		margin: 20px 0;
		font-size: 16px;
	}
	
	.login_logo {
		height: 180px;
		width: 100%;
		line-height: 240px;
		display: block;
		text-align: center;
		background-color: #2B2EF5;
		img {
			width: 129px;
			height: 71px;
			display: inline-block;
		}
	}
	
	.content6 {
		padding: 10px 20px;
		background-color: #FFFFFF;
		.suk2 {
			margin: 15px 0 0 0;
			color: #000000;
		}
	}
</style>


