<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>专利管理
			<p class="title_right" @click='zlfb'>去发布</p>
		</Header>
		<Content class="content6 top_p2">
			<div class="hm_product_num">
				<Tabs value="name1">
					<TabPane label="出售中" name="name1">
						<ul>
							<li class="item" v-for="(item, name, index) in product_list">
								<span>
								<img :src="item.bannerUrl" />
								<span><p class="title">{{item.name}}</p><p class="small_bh">编号：{{item.id}}</p><p class="price">¥{{item.price}}</p></span>
								</span>
								<span><Button @click='zlbj(item.id)' type="primary" shape="circle"> 编辑</Button>
								<Button type="primary" shape="circle" @click="ok(item.id)">删除</Button>
								</span>
							</li>
						</ul>
					</TabPane>
					<TabPane label="交易中" name="name2" span="8">
						<ul>
							<li class="item" v-for="(item, name, index) in product_list1">
								<img :src="item.bannerUrl" />
								<span><p class="title">{{item.name}}</p><p class="small_bh">编号：{{item.id}}</p><p class="price">¥{{item.price}}</p></span>
							</li>
						</ul>
					</TabPane>
					<TabPane label="已出售" name="name3" span="8">
						<ul>
							<li class="item" v-for="(item, name, index) in product_list2">
								<span>
								<img :src="item.bannerUrl" />
								<span><p class="title">{{item.name}}</p><p class="small_bh">编号：{{item.id}}</p><p class="price">¥{{item.price}}</p></span>
								</span>
								<span style="width:100%; float: right;">
								<Button type="primary" shape="circle" @click="modal2 = true">删除</Button> 
								</span>
							</li>
						</ul>
					</TabPane>

				</Tabs>

			</div>
		</Content>
	</div>
</template>
<script>
	import { patentgetList, patentdelete } from '../../api/api';
	export default {
		name: 'zlgl',
		data() {
			return {
				modal2: false,
				product_list: [],
				product_list1: [],
				product_list2: []

			}
		},
		components: {

		},
		methods: { 
			onload() { 
				if(sessionStorage.customerId == null) {
					this.$router.push({
						path: '/login'
					})
				}
			},
			//获取专利列表
			getProducts() {
				let para = {
					customerId: sessionStorage.customerId,
					pageSize:20,
					status:0,
				};
				this.listLoading = true;
				//NProgress.start();
				patentgetList(para).then((res) => {
					this.total = res.data.result.total;
					this.product_list = res.data.result.result;
					//console.log(this.product_list);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			getProducts1() {
				let para = {
					customerId: sessionStorage.customerId,
					pageSize:20,
					status:1,
				};
				this.listLoading = true;
				//NProgress.start();
				patentgetList(para).then((res) => {
					this.total = res.data.result.total;
					this.product_list1 = res.data.result.result;
					//console.log(this.product_list);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			getProducts2() {
				let para = {
					customerId: sessionStorage.customerId,
					pageSize:20,
					status:2,
				};
				this.listLoading = true;
				//NProgress.start();
				patentgetList(para).then((res) => {
					this.total = res.data.result.total;
					this.product_list2 = res.data.result.result;
					//console.log(this.product_list);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			prev() {
				this.$router.push({
					path: '/my',  
				})
			},
			zlfb() {
				this.$router.push({
					path: '/zlfb', 
				})
			},
			zlbj(id) {
				this.$router.push({
					path: '/zlbj', 
                    query:{  
						id:id
                    } 
				})
			},
			ok(id) {
				let para = {
					customerId: sessionStorage.customerId,
					ids:id,
				};
				patentdelete(para).then((res) => {  
					this.listLoading = false; 
					this.$Message.success('专利删除成功');
					this.getProducts();
				}).catch((err) => {
					console.log(err);
					this.$Message.success('专利删除失败');
				});
			},
			cancel() {
				this.$Message.warning('已取消删除');
			},
			fenx() {
				this.$Message.success({
					content: '分享成功',
					duration: 3
				});
			},
		},
		props: {},
		mounted() {
			this.getProducts();
			this.getProducts1();
			this.getProducts2();
			this.onload();
		}
	}
</script>

<style lang="less" scoped>
	.title_right {
		display: inline-block;
		float: right;
	}
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		ul {
			width: 100%;
			margin-top: -16px;
			.item {
				padding: 20px 0;
				display: block;
				border-bottom: 1px solid #F6F6F6;
				height: auto !important;
				min-height: 20px;
				overflow: hidden;
				width: 100%;
				&:last-child{
					border: none;
				}
				button {
					background: #F4F4F4;
					border: 1px solid #E5E5E5;
					border-radius: 23px;
					width: 96px;
					height: 33px;
					line-height: 21px;
					font-size: 16px;
					float: left;
					margin-top: 15px;
					color: #666;
					margin: 15px 6px 0px 6px;
				}
				button:hover {
					color: #FFFFFF;
					background: #2B2EF5;
					border: none;
				}
			}
			img {
				height: 78px;
				width: 130px;
				border-radius: 3px;
				display: flex;
				float: left;
				margin-right: 10px;
			}
			span {
				display: inline-block;
				float: left;
				font-size: 16px;
				color: #000000;
				text-align: left;
				.title {
					font-size: 16px;
					color: #000000;
					font-weight: bold;
				}
				.small_bh {
					font-size: 12px;
					color: #9B9B9B;
				}
				.price {
					font-size: 20px;
					color: #2B2EF5;
					font-weight: bold;
				}
			}
		}
	}
</style>


