<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>
			</router-link>
			{{categoryFir}}·专利</Header>
		<Content class="content6 top_p bottom_p">
			<div class="hm_product_num">
				<Row type="flex" :gutter="16">
					<col span="12">
					<p v-for="(item, name, index) in cate_list" @click="zl_list(item.categorySec)" class="zl_meau_box">
						<img :src=item.src /> {{item.categorySec}}
					</p>
					</col>
				</Row>

			</div>
		</Content>
	</div>
</template>

<script>
	import footmeau from './../public/footmeau.vue';
	import sb_all from './sb_all.vue'
	export default {
		name: 'zl_meau',
		data() {
			return {
				categoryFir: this.$route.query.categoryFir,
				cate_list: [{
					categorySec: '发明型',
					src: require('../../assets/fmx.png'),
				}, {
					categorySec: '实用型',
					src: require('../../assets/syx.png'),
				}, {
					categorySec: '外观型',
					src: require('../../assets/wgx.png'),
				}]
			}
		},
		components: {
			footmeau
		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			zl_list(categorySec) {
				this.$router.push({
					path: '/zl_list',
					query: {
						type: this.$route.query.categoryFir,
						categoryFir: this.$route.query.value,
						categorySec: categorySec, 
                        applyType: this.$route.query.applyType,
					}
				})
			},
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;
		.zl_meau_box {
			width: 100%;
			display: block;
			font-size: 16px;
			color: #000000;
			height: 140px;
			margin: 35px auto 20px auto;
			text-align: center;
			img {
				height: 93px;
				width: 93px;
				display: block;
				margin: 0 auto 5px auto;
			}
		}
	}
</style>


