<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>{{categoryFir}}·商标</Header>
		<Content class="content6 top_p2">
			<div class="hm_product_num">
				<ul>
					<li class="item" @click='sb_details(item.id,item.name,item.price)' v-for="(item, name, index) in product_list">
						<img :src="item.bannerUrl" />
						<span><p class="title">{{item.name}}</p><p class="small_bh">编号：{{item.id}}</p><p class="price">¥{{item.price}}</p></span>
					</li>
				</ul>
			</div>
		</Content>
	</div>
</template>

<script>
	import { brandgetList } from '../../api/api';
	export default {
		name: 'sb_list',
		data() {
			return {
				product_list: [],
				categoryFir:this.$route.query.categoryFir
			}
		},
		components: {},
		props: {},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			//获取产品列表
			getProducts() {
				let categoryFir = this.$route.query.categoryFir;
				let vipFlag = this.$route.query.vipFlag;
				let inlandFlag = this.$route.query.inlandFlag;
				let areaCode = this.$route.query.areaCode 
				//console.log(this.$route.query.areaCode);
				let para = { 
					categoryFir: categoryFir,
					pageNum: this.pageNum,
					pageSize: this.pageSize,
					vipFlag:vipFlag,
					inlandFlag:inlandFlag,
                    areaCode:areaCode,
				};
				this.listLoading = true;
				//NProgress.start();
				brandgetList(para).then((res) => {
				// console.log(res);
					this.product_list = res.data.result.result;
				// console.log(this.product_list);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
				// console.log(err);
				});
			},
			sb_details(id,name,price) {
				this.$router.push({
					path: '/sb_details',
					query: {
						id: id, // orderNum : this.searchData.orderNo
						applyType: this.$route.query.applyType,
						amount: this.$route.query.price,
						name: name,
						price: price,
					}
				})
			}

		},
		mounted() {
			this.getProducts();
		}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		display: flex;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;
		ul {
			width: 100%;
			.item {
				padding: 20px 0;
				display: block;
				border-bottom: 1px solid #F6F6F6;
				height: 120px;
				width: 100%;
			}
			img {
				height: 78px;
				width: 130px;
				border-radius: 3px;
				display: flex;
				float: left;
				margin-right: 10px;
			}
			span {
				display: inline-block;
				float: left;
				font-size: 16px;
				color: #000000;
				text-align: left;
				.title {
					font-size: 16px;
					color: #000000;
					font-weight: bold;
				}
				.small_bh {
					font-size: 12px;
					color: #9B9B9B;
				}
				.price {
					font-size: 20px;
					color: #2B2EF5;
					font-weight: bold;
				}
			}
		}
	}
</style>


