<template>
	<div class="layout">
		<Header class="header">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" /> 个人资料
			</p>
		</Header>
		<div class="content6">
			<Form ref="formCustom" :data="formCustom" :model="formCustom" :rules="ruleCustom" class="big_h" :label-width="90">
				<FormItem class="my_list" style="margin-top: -10px;" :label-width="0">
					<Upload v-model="formCustom.headLogo" class="top_p" ref="upload" :show-upload-list="false" :default-file-list="defaultList" :on-success="handleSuccess" :format="['jpg','jpeg','png']" :max-size="2048" :on-format-error="handleFormatError" :on-exceeded-size="handleMaxSize" :before-upload="handleBeforeUpload" multiple type="drag" action="http://58.87.66.77:80/v1/upload/image">
						<div style="padding: 20px 0; font-size: 18px; color: #999">
							<Icon type="ios-cloud-upload" size="52" style=""></Icon>
							<p>上传</p>
						</div>
					</Upload>
					<div class="demo-upload-list" v-for="item in uploadList">
						<img :src=formCustom.headLogo class="img_box">
						<div class="demo-upload-list-cover">
							<Icon type="ios-trash-outline" style="font-size: 22px; color: #0000FF;" @click.native="handleRemove(item)"></Icon>
						</div>
					</div>
				</FormItem>
				<FormItem class="my_list" prop="loginName" label="用户名">
					<input v-model="formCustom.loginName" type="text" />
				</FormItem>

				<FormItem class="my_list" prop="name" label="姓名">
					<input v-model="formCustom.name" type="text" />
				</FormItem>
				<FormItem class="my_list" prop="mobile" label="手机号码">
					<input v-model="formCustom.mobile" type="number" />
				</FormItem>
				<Button type="primary" shape="circle" @click="handleSubmit('formCustom')">立即发布</Button>
			</Form>

		</div>
	</div>
</template>

<script>
	import { getUseredite, getcustomergetById } from '../../api/api';
	export default {
		name: 'glzh',
		data() {
			return {

				formCustom: {
					loginName: ' ',
					mobile: ''
				},
				ruleCustom: {
					mobile: [{
						required: true,
						message: '不能为空',
						trigger: 'blur',
					}, {
						type: 'string',
						pattern: /^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/,
						message: '请输入正确的电话',
						trigger: 'blur',
					}],
					loginName: [{
						required: true,
						message: ' ',
						trigger: 'blur'
					}],
					name: [{
						required: true,
						message: ' ',
						trigger: 'blur'
					}],
				},
				visible: false,
				defaultList: [{
					'name': 'a42bdcc1178e62b4694c830f028db5c0',
					'url': 'https://o5wwk8baw.qnssl.com/a42bdcc1178e62b4694c830f028db5c0/avatar'
				}],
				uploadList: [],
			}
		},
		components: {

		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			onload() {
				if(sessionStorage.customerId == null) {
					this.$router.push({
						path: '/login'
					})
				}
			},
			handleRemove(file) {
				const fileList = this.$refs.upload.fileList;
				this.$refs.upload.fileList.splice(fileList.indexOf(file), 1);
			},
			handleSuccess(res, file) {
				file.headLogo = 'http://58.87.66.77:80' + res.result;
				this.formCustom.headLogo = 'http://58.87.66.77:80' + res.result;
			},
			handleFormatError(file) {
				this.$Notice.warning({
					title: '文件格式不正确',
					desc: '文件格式的' + file.name + '是否正确，请选择jpg或png.'
				});
			},
			handleMaxSize(file) {
				this.$Notice.warning({
					title: '超过文件大小限制',
					desc: 'File  ' + file.name + '太大了，不能超过2M.'
				});
			},
			handleBeforeUpload() {
				const check = this.uploadList.length < 1;
				if(!check) {
					this.$Notice.warning({
						title: '最多可以上传一张照片.'
					});
				}
				return check;
			},
			getuser() {
				this.listLoading = true;
				//NProgress.start();
				getcustomergetById().then((res) => {
					this.formCustom = res.data.result;
					//console.log(this.formCustom);
					let headLogo = this.formCustom.headLogo;
					this.listLoading = false;
				}).catch((err) => {
					//console.log(err);
				});
			},
			handleSubmit(name) {
				this.$refs[name].validate((valid) => {
					if(valid) {
						let para = {
							headLogo: this.formCustom.headLogo,
							loginName: this.formCustom.loginName,
							mobile: this.formCustom.mobile,
							name: this.formCustom.name,
							customerId: sessionStorage.customerId,

						};
						getUseredite(para).then((res) => {
							if(res.data.code == 0) {
								sessionStorage.sallerMobile = this.formCustom.mobile;
								this.$Message.success('资料修改成功!');
								this.$router.push({
									path: '/my'
								})
							} else {
								this.$Message.warning(res.data.message);
							}
						});
					} else {
						this.$Message.error('输入有误');
					}
				})
			},
		},
		mounted() {
			this.onload();
			this.getuser();
			this.uploadList = this.$refs.upload.fileList;
		},
	}
</script>

<style lang="less" scoped>
	.login_logo {
		height: 180px;
		width: 100%;
		line-height: 240px;
		display: block;
		text-align: center;
		background-color: #2B2EF5;
		img {
			width: 129px;
			height: 71px;
			display: inline-block;
		}
	}
	
	.content6 {
		padding: 10px 20px;
		background-color: #FFFFFF;
		.suk {
			margin: 10px 0;
			display: block;
			background-color: #F6F6F6;
			border-radius: 50px;
		}
		.my_list {
			border-bottom: 1px solid #E6E6E6;
			display: block;
			font-size: 16px;
			-webkit-box-pack: justify;
			-ms-flex-pack: justify;
			justify-content: space-between;
			padding-top: 10px;
			margin-top: -20px;
			line-height: 50px;
			input {
				width: 100%;
				font-size: 16px;
			}
			.img_box {
				width: 100px;
				height: 100px;
				border-radius: 100px;
				position: absolute;
				z-index: 99;
				left: 136px;
				top: 12px;
			}
		}
		.but_left {
			float: left;
			font-size: 14px;
		}
		.but_right {
			float: right;
			font-size: 14px;
		}
	}
</style>


