<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>{{name}}</Header>
		<Content class="content6 top_p" :model="products">
			<div class="hm_product_num">
				<div class="new_title">
					<p class="title">{{products.title}}</p>
					<span><div class="small_bh">预算：<p class="price_big">{{products.budget}}</p></div>
				<p class="price">发布日期：{{products.createTime}}</p></span>
				</div>
						<Row :gutter="16" class="big_h">
							
							<Col span="24">
							<div class="my_list">
								<span class="left">
									<p class="title">使用范围</p></span>
									<p class="title">{{products.description}}</p></span>
							</div>
							</Col>
						</Row>
				<Button type="primary" shape="circle">买家联系方式：{{telphone}}</Button>
				<img src="../../assets/banner-mid.png" style="width: 100%; height:100%;"/>
			</div>
		</Content>
	</div>
</template>

<script>
	import { CollectgetById } from '../../api/api';
	export default {
		name: 'qiugou_details',
		data() {
			return {
				products:[],
				name: '商标求购详情',
				title: '',
				budget: '',
				createTime: '',
				description: '', 
				telphone: '18798773322',

			}
		},
		components: {

		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			//获取产品列表
			getProducts() {
				this.listLoading = true;
				//NProgress.start(); 
				let para = {
					id:this.$route.query.id,
					actionType: this.$route.query.actionType,
					contentType: this.$route.query.contentType,
				};
				CollectgetById(para).then((res) => {
					//console.log(res); 
					this.products = res.data.result;
					console.log(this.products); 
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
		},
		props: {},
		mounted() {
			this.getProducts();
		}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		.big_h { 
			margin-bottom: 30px;
			.my_list {
				overflow: hidden;
				height: 60px;
				line-height: 60px;
				border-bottom: 1px solid #E6E6E6;
				display: flex;
				padding-right: 20px;
				font-size: 16px;
				justify-content: space-between;
				.left {
					align-content: flex-start;
					.input_class {
						background-color: #FFFFFF;
						border: none;
					}
					.title {
						border-radius: 0 0 6px 6px;
						bottom: 0px;
						color: #000000;
						margin: 0;
						float: left;
						display: inline-block;
						width: 100px;
						text-align: left;
						align-content: flex-start;
					}
				}
				input {
					border: none;
					outline: none;
					font-size: 16px;
					display: inline-block;
					float: right;
					height: 58px;
					width: 260px;
					text-align: right;
				}
			}
		}
		.new_title {
			flex: 2;
			font-size: 18px;
			color: #000000;
			width: 100%;
			line-height: 22px;
			text-align: left;
			padding: 10px 0 20px 0;
			font-weight: bold;
			border-bottom: 1px solid #E6E6E6;
			span {
				width: 100%;
				display: block;
				width: 100%;
				display: flex;
				justify-content: space-between;
				line-height: 36px;
				font-size: 15px;
				color: #666666;
				font-weight: normal;
				.small_bh {
					text-align: left;
					flex: 1;
					align-items: left;
				.price_big {
					color: #2B2EF5; 
					display: inline-block;
				}
				}
				.price {
					text-align: right; 
					align-items: right;
				}
			}
		}
		.de_cont {
			padding: 20px;
		}
		.art_cont {
			margin: 20px 0px;
			display: block;
			font-size: 14px;
			color: #000000;
			line-height: 26px;
			text-align: left;
		}
	}
</style>


