<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>{{service.name}}</Header> 
		<Content class="top_p2" :model="service">
		<img class="bg_box" :src=service.bannerUrl />
			<div class="hm_product_num">
				<Row type="flex" :gutter="16" class="de_cont">
					<col span="12">
					<p>价格: <span class="cl_num">{{service.price}}</span></p>
					</col>
					<col span="12">
					<p>成交记录:{{service.total}}</p>
					</col>
				</Row>
				<p class="line"></p>
				<Row type="flex" :gutter="16" class="de_cont  bottom_p">
					<col span="12">
					<p class="cont" v-html='service.content' ></p>
					</col>
				</Row>
			</div>
		</Content>
		<buyservice></buyservice>
	</div>
</template>

<script>
	import { servicegetList, servicecreate, serviceupdate, servicedelete, servicegetById } from '../../api/api';
	import buyservice from './../public/buyservice.vue';
	export default {
		name: 'service_details',
		data() {
			return { 
				name:'',
				price:'',
				total:'',
				content: '',
				bannerUrl:'',
				service:[],
			}
		},
		components: {
			buyservice
		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			//获取产品类别列表
			getservice() {
				let para = { 
					id:this.$route.query.id
				};
				this.listLoading = true;
				servicegetById(para).then((res) => {  
					this.service = res.data.result; 
					this.price=this.service.price; 
					this.$route.query.price=this.price; 
					this.listLoading = false; 
				});
			},
		},
		props: {},
		mounted() {
			this.getservice(); 
		}
	}
</script>

<style lang="less" scoped>
	.bg_box { 
		width: 100%;
		height: 152px; 
		display: block;
		font-size: 23px;
		color: #FFFFFF;
		text-align: center;
		line-height: 122px;
		font-weight: bolder;
	}
	
	.top {
		margin-top: 152px;
	}
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
	    .de_cont {
			padding:15px 20px 10px 20px;
		}
		.cl_num{
			color: #2B2EF5;
			font-size: 26px;
		}
		span {
			display: inline-block;
			text-align: left;
			font-weight: bold;
		}
		p {
			font-size: 15px;
			color: #000000;
			line-height: 22px;
			width: 50%;
			text-align: left;
			padding: 7px;
		}
		.cont {
			font-size: 14px;
			color: #000000;
			width: 100%;
			line-height: 25px;
		}
		.line {
			width: 100%;
			background-color: #D7D7D7;
			height: 1px;
			padding: 0;
		}
	}
</style>


