<template>
	<Footer class="footer">
		<Row type="flex">
			<col span="12">
			<p @click="shouc"><img src="../../assets/shoucang.png" style="margin-right: 10px;" />收藏</p>
			</col> 
			<col span="12">
			<p class="buy" @click="ok"><img src="../../assets/goumai.png" />立即购买</p>
			</col> 
		</Row>
	</Footer>
</template>

<script>
	import {
		Collectcreate,  
	} from '../../api/api';
	export default {
		name: 'buyservice',
		data() {
			return {
				modal2: false,
				sczt:1,
			}
		},
		methods: {
			onload() {
				if(sessionStorage.customerId == null) {
					this.$router.push({
						path: '/login'
					})
				}
			},
			ok() {
				this.$Message.success('开始订单支付');
				this.$router.push({
					path: '/zhifuding',
					query: {
						id: this.$route.query.id,
                        applyType:this.$route.query.applyType,  
						name:this.$route.query.name,
						price:this.$route.query.price,
					}
				}) 
			},  
			shouc() { 
				this.listLoading = true;
				//NProgress.start();  
				let	applyType= this.$route.query.applyType;
				let para={ 
					actionType:'2',
					contentType: applyType,
					targetId: this.$route.query.id,
					customerId:sessionStorage.customerId
				};
				Collectcreate(para).then((res) => {
					this.products = res.data.result;  
					this.listLoading = false; 
					let sczt = 2;  
				this.$Message.success({
					content: '收藏成功',
					duration: 3
				}); 
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				}); 
			},
		},
		mounted() {
			//this.page = Number(sessionStorage.pageIndex); 
			this.onload(); 
		}
	}
</script>

<style lang="less" scoped>
	.tkwz {
		font-size: 18px;
		color: #000000;
	}
	
	.footer {
		background-color: #FFFFFF;
		z-index: 999;
		bottom: -1px;
		width: 100%;
		position: fixed;
		padding: 0;
		box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.3);
		p {
			width: 50%;
			line-height: 60px;
			text-align: center;
			color: #0000FF;
			font-size: 18px;
		}
		img {
			display: inline-block;
			margin: auto;
			vertical-align: middle;
		}
		.line {
			margin-left: -1px;
			margin-top: 15px;
			height: 30px;
			width: 1px;
			display: inline-block;
			vertical-align: middle;
			background-color: #D7D7D7;
		}
		.buy {
			background-color: #0000FF;
			display: block;
			height: 60px;
			width: 50%;
			color: #FFFFFF;
			font-size: 18px;
			img {
				display: inline-block;
				margin-right: 5px;
				vertical-align: middle;
			}
		}
	}
</style>


