<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>我的订单 </Header>
		<Content class="content6 top_p2">
			<div class="hm_product_num">
				<Tabs value="name1">
					<TabPane label="商标类" name="name1">
						<ul>
							<li class="item" v-for="(item, name, index) in product_list">
								<span><p class="title">{{item.targetJson.name}}</p>
									<p style="width:180px; float: left;" class="small_bh">编号：{{item.targetJson.id}}</p>
									<p  v-if='item.targetJson.status===0' style="width: 180px; float: right;" class="small_bh">状态：未付款</p>
									<p  v-else-if='item.targetJson.status===1' style="width: 180px; float: right;" class="small_bh">状态：已付款</p>
									<p  v-else-if='item.targetJson.status===2' style="width: 180px; float: right;" class="small_bh">状态：已完成</p> 
									<p  v-else-if='item.targetJson.status===3' style="width: 180px; float: right;" class="small_bh">状态：已取消</p> 
									<p class="price">¥{{item.amount}}</p></span>
								<span style="width:100%; float:right;">
								<Button type="primary" shape="circle" @click="modal31 = true" v-if='item.targetJson.status===0'> 取消订单</Button> 
								<Button type="primary" shape="circle" @click="modal21 = true" v-else='item.targetJson.status===1'>删除订单</Button>
								<Button type="primary" shape="circle" @click="modal21 = true" v-else='item.targetJson.status===2'>删除订单</Button>
								<Button type="primary" shape="circle" @click="modal21 = true" v-else='item.targetJson.status===3'>删除订单</Button>
								<Modal v-model="modal21" @on-ok="ok1(item.targetJson.id)" @on-cancel="cancel">
				                  <p class="tkwz">确认删除订单？</p>
								</Modal>
								<Modal v-model="modal31" @on-ok="ok12(item.targetJson.id)" @on-cancel="cancel">
				                  <p class="tkwz">确认取消订单？</p>
								</Modal>
								</span>
							</li>
						</ul>
					</TabPane>
					<TabPane label="专利类" name="name2" span="8">
						<ul>
							<li class="item" v-for="(item, name, index) in product_list2">
								<span><p class="title">{{item.targetJson.name}}</p>
									<p style="width:180px; float: left;" class="small_bh">编号：{{item.targetJson.id}}</p>
									<p  v-if='item.targetJson.status===0' style="width:180px; float:right;" class="small_bh">状态：未付款</p>
									<p  v-else-if='item.targetJson.status===1' style="width:180px; float:right;" class="small_bh">状态：已付款</p>
									<p  v-else-if='item.targetJson.status===2' style="width:180px; float:right;" class="small_bh">状态：已完成</p> 
									<p  v-else-if='item.targetJson.status===3' style="width:180px; float:right;" class="small_bh">状态：已取消</p> 
									<p class="price">¥{{item.amount}}</p></span>
								<span style="width:100%; float: right;">
								<Button type="primary" shape="circle" @click="modal32 = true" v-if='item.targetJson.status===1'> 取消订单</Button> 
								<Button type="primary" shape="circle" @click="modal22= true" v-else='item.targetJson.status===0'>删除订单</Button>
								<Button type="primary" shape="circle" @click="modal22= true" v-else='item.targetJson.status===2'>删除订单</Button>
								<Button type="primary" shape="circle" @click="modal22= true" v-else='item.targetJson.status===3'>删除订单</Button>
								<Modal v-model="modal22" @on-ok="ok(item.targetJson.id)" @on-cancel="cancel">
				                  <p class="tkwz">确认删除订单？</p>
								</Modal>
								<Modal v-model="modal32" @on-ok="ok2(item.targetJson.id)" @on-cancel="cancel">
				                  <p class="tkwz">确认取消订单？</p>
								</Modal>
								</span>
							</li>
						</ul>
					</TabPane>
				</Tabs>

			</div>
		</Content>
	</div>
</template>
<script>
	import { applylist, applydelete, applyupdate } from '../../api/api';
	export default {
		name: 'zlgl',
		data() {
			return {
				modal21: false,
				modal31: false,
				modal22: false,
				modal32: false,
				product_list: [],
				product_list2: [],

			}
		},
		components: {

		},
		methods: {
			onload() {
				if(sessionStorage.customerId == null) {
					this.$router.push({
						path: '/login'
					})
				}
			}, //获取商标列表
			getProducts1() {
				let para = {
					customerId: sessionStorage.customerId,
					applyType: 1,
					pageSize: 100
				};
				this.listLoading = true;
				//NProgress.start();
				applylist(para).then((res) => {
					this.total = res.data.result.total;
					this.product_list = res.data.result.result;
					//console.log(this.product_list);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			//获取商标列表
			getProducts2() {
				let para = {
					customerId: sessionStorage.customerId,
					applyType: 2,
					pageSize: 100
				};
				this.listLoading = true;
				//NProgress.start();
				applylist(para).then((res) => {
					this.total = res.data.result.total;
					this.product_list2 = res.data.result.result;
					console.log(this.product_list2);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
				});
			},
			prev() {
				this.$router.go(-1)
			},
			ok1(id) {
				let para = {
					customerId: sessionStorage.customerId,
					id: id,
					applyType: 1,
				};
				applydelete(para).then((res) => {
					this.$Message.success('操作成功');
					this.getProducts1()
					//NProgress.done();
				}).catch((err) => {
					this.$Message.warning('err');
				});
			},
			ok12(id) {
				let para = {
					customerId: sessionStorage.customerId,
					id: id,
					targetJson:{status: '3'},
					applyType: 1,
				};
				applyupdate(para).then((res) => {
					this.$Message.success('操作成功');
					this.getProducts1()
					//NProgress.done();
				}).catch((err) => {
					this.$Message.warning('err');
				});
			},
			ok(id) {
				let para = {
					customerId: sessionStorage.customerId,
					id: id,
					applyType: 2,
				};
				//NProgress.start();
				applydelete(para).then((res) => {
					this.$Message.success('操作成功');
					this.getProducts2()
					//NProgress.done();
				}).catch((err) => {
					this.$Message.warning('err');
				});
			},
			ok2(id) {
				let para = {
					customerId: sessionStorage.customerId,
					id: id,
					targetJson:{status: '3'},
					applyType: 2,
				};
				applyupdate(para).then((res) => {
					this.$Message.success('操作成功');
					this.getProducts2()
					//NProgress.done();
				}).catch((err) => {
					this.$Message.warning('err');
				});
			},
			cancel() {
				this.$Message.warning('取消操作');
			},
		},
		props: {},
		mounted() {
			this.onload();
			this.getProducts2();
			this.getProducts1()
		}
	}
</script>

<style lang="less" scoped>
	.title_right {
		display: inline-block;
		float: right;
	}
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		ul {
			width: 100%;
			margin-top: -16px;
			.item {
				padding: 20px 0;
				display: block;
				border-bottom: 1px solid #F6F6F6;
				height: auto !important;
				min-height: 20px;
				overflow: hidden;
				width: 100%;
				&:last-child{
					border: none;
				}
				button {
					background: #F4F4F4;
					border: 1px solid #E5E5E5;
					border-radius: 23px;
					width: 96px;
					height: 33px;
					line-height: 21px;
					font-size: 16px;
					float: left;
					margin-top: 15px;
					color: #666;
					margin: 15px 6px 0px 6px;
				}
				button:hover {
					color: #FFFFFF;
					background: #2B2EF5;
					border: none;
				}
			}
			img {
				height: 78px;
				width: 130px;
				border-radius: 3px;
				display: flex;
				float: left;
				margin-right: 10px;
			}
			span {
				display: inline-block;
				float: left;
				font-size: 16px;
				color: #000000;
				text-align: left;
				.title {
					font-size: 16px;
					color: #000000;
					font-weight: bold;
				}
				.small_bh {
					font-size: 12px;
					color: #9B9B9B;
				}
				.price {
					font-size: 20px;
					color: #2B2EF5;
					font-weight: bold;
				}
			}
		}
	}
</style>


