<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>{{products.name}}</Header>
		<Content class="content6 top_p">
			<div class="hm_product_num" v-model="products">
				<div class="new_title">
				<p class="title">{{products.name}}</p>
				<span><p class="small_bh">浏览：{{products.total}}人</p>
				<p class="price">发布日期：{{products.createTime}}</p></span>
				</div>
				<span class="art_cont" v-html="products.content"></span>
			</div>

		</Content>
	</div>
</template>

<script>
	import {servicegetById} from '../../api/api';
	export default {
		name: 'new_details',
		data() {
			return {
				products:[],
				name: '',
				total:'',
				renshu: '',
				createTime: '',
				content:'',

			}
		},
		components: {
			
		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			//获取产品列表
			getProducts() {
				this.listLoading = true;
				//NProgress.start(); 
				let para = {
					id: this.$route.query.id,
					type: this.$route.query.type,
				};
				servicegetById(para).then((res) => {
					this.products = res.data.result;
					//console.log(this.products);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
		},
		props: {},
		mounted() {
			this.getProducts();
		}
	}
</script>

<style lang="less" scoped>
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		.new_title {
			flex: 2;
			font-size: 18px;
			color: #000000;
			width: 100%;
			line-height: 22px;
			text-align: left;
			padding: 10px 0 20px 0;
			font-weight: bold;
			border-bottom: 1px solid #E6E6E6;
			span {
				width: 100%;
				display: block;
				width: 100%;
				display: flex;
				justify-content: space-between;
				line-height: 36px;
				font-size: 15px;
				color: #666666;
			font-weight: normal;
				.small_bh {
					text-align: left;
					flex: 1;
					align-items: left;
				}
				.price {
					text-align: right;
					flex: 1;
					align-items: right;
				}
			}
		}
		.de_cont {
			padding: 20px;
		}
		
			.art_cont {
				margin: 20px 0px;
				display: block;
				font-size: 14px;
				color: #000000;
				line-height: 26px;
				text-align: left;
			}
	}
</style>


