<template>
	<div class="layout">
		<Header class="header3">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>{{title}}</Header>
		<div class="bg_box">{{slogin}}</div>
		<Content class="top">
			<div class="hm_product_num">
				<Row type="flex" :gutter="16" class="de_cont">
					<col span="12">
					<p>价格: <span class="cl_num">{{price}}</span></p>
					</col>
					<col span="12">
					<p>成交记录:{{service}}</p>
					</col>
				</Row>
				<p class="line"></p>
				<Row type="flex" :gutter="16" class="de_cont  bottom_p">
					<col span="12">
					<p class="cont" v-html="cont"></p>
					</col>
				</Row>
			</div> 
		</Content>
		<buyservice></buyservice>
	</div>
</template>

<script>
	import buyservice from './../public/buyservice.vue';
	export default {
		name: 'gjsbfw_detail',
		data() {
			return {
				title:'美国商标服务',
				slogin:'国际商标服务，选择中源商标！',
				src: require('../../assets/deta_img.png'),
				price: '¥6000',
				service: '¥600',
				cont: '商标续展所需材料</br>1、申请人的主体资格证明【 公司： 营业执照（ 盖公章）； 个人： 身份证复印件】</br>2、商标续展申请书</br>3、商标代理委托书</br>4、商标注册证复印件</br>准备好上述资料后可以自己到商标局提交申请， 也可以委托标池商标代理机构办理。</br>商标续展大约需要4-6月时间。</br>注册商标的有效期为10年。 注册商标有效期满后需要继续使用的， 应当在期满前的一年内申请续展。 在此期间未能提出申请的， 还有6个月的宽展期。 宽展期内仍未提出申请的， 期满后商标局将予以注销。 自商标局受理注册商标续展申请只日起六至七个月， 由商标局审查核准符合续展条件后发布公告并发正式续展证明。 ',

			}
		},
		components: {
			buyservice
		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.bg_box {
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 152px;
		padding-top: 30px;
		display: block;
		background-image: url('../../assets/ser_bg.png');
		font-size: 23px;
		color: #FFFFFF;
		text-align: center;
		line-height: 122px;
		font-weight: bolder;
	}
	
	.top {
		margin-top: 152px;
	}
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
	    .de_cont {
			padding:15px 20px 10px 20px;
		}
		.cl_num{
			color: #2B2EF5;
			font-size: 26px;
		}
		span {
			display: inline-block;
			text-align: left;
			font-weight: bold;
		}
		p {
			font-size: 15px;
			color: #000000;
			line-height: 22px;
			width: 50%;
			text-align: left;
			padding: 7px;
		}
		.cont {
			font-size: 14px;
			color: #000000;
			width: 100%;
			line-height: 25px;
		}
		.line {
			width: 100%;
			background-color: #D7D7D7;
			height: 1px;
			padding: 0;
		}
	}
</style>


