<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>{{title}}</Header>
		<Content class="content2 top_p">
			<div class="vip_box">
				¥2000/年
			</div>
		</Content>
		<Content class="content6">
			<div class="hm_product_num">
				<Row :gutter="16">
					<Col span="24">
					<span class="big_title"><img src="../../assets/add.png" /> 会员权益</span>
					</Col>
					<Col span="24">
					<div class="my_list" v-for="item in yhj_list">
						{{item.title}}
					</div>
					</Col>
				</Row>

				<Button type="primary" shape="circle" @click="ok">立即升级</Button>
			</div>
		</Content>
	</div>
</template>
<script>
	export default {
		name: 'sjvip',
		data() {
			return {
				price: '¥2000',
				title: '升级VIP',
				yhj_list: [{
						title: '平台所有交易服务费9折优惠券',
				},
				]
			}
		},
		components: {

		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			sjvip() {
				this.$router.push({
					path: '/sjvip'
				})
			},
			ok() {
				this.$Message.success('提交成功'); 
				this.$router.push({
					path: '/my'
				})
			},
			cancel() {
				this.$Message.warning('提交失败');
			}
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.vip_box {
		height: 162px;
		line-height: 162px;
		width: 382px;
		display: block;
		margin: auto;
		background-color: #E2B500;
		border-radius: 6px;
		color: #FFFFFF;
		font-size:32px;
		font-weight: bold;
	}
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		.big_title {
			font-size: 18px;
			color: #333333;
			line-height: 50px;
			vertical-align: middle;
		}
		.my_list {
			border-radius: 0 0 6px 6px;
			font-size: 16px;
			display: block;
			color: #000000;
			margin: 0;
			width: 100%;
			background: url(../../assets/vip_bg.png) 100% no-repeat;
			background-position: 0px 0px;
			height: 45px;
			text-align: left;
			line-height: 45px;
			text-indent: 16px;
			margin-bottom: 15px;
		}
	}
</style>


