<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>商标管理
			<p class="title_right" @click='sbfb()'>去发布</p>
		</Header>
		<Content class="content6 top_p2">
			<div class="hm_product_num">
				<Tabs value="name1">
					<TabPane label="出售中" name="name1">
						<ul>
							<li class="item" v-for="(item, name, index) in product_list">
								<span><img :src="item.bannerUrl" />
								<span><p class="title">{{item.name}}</p><p class="small_bh">编号：{{item.id}}</p><p class="price">¥{{item.price}}</p></span>
								</span>
								<span><Button @click='sbbj(item.id,item.categoryFir,item.inlandFlag,item.areaCode,item.publicFlag)' type="primary" shape="circle"> 编辑</Button>
								<Button type="primary" shape="circle" @click="ok(item.id)">删除</Button>
								</span>
							</li>
						</ul>
					</TabPane>
					<TabPane label="交易中" name="name2" span="8">
						<ul>
							<li class="item" v-for="(item, name, index) in product_list1">
								<img :src="item.bannerUrl" />
								<span><p class="title">{{item.name}}</p><p class="small_bh">编号：{{item.id}}</p><p class="price">¥{{item.price}}</p></span>

							</li>
						</ul>
					</TabPane>
					<TabPane label="已出售" name="name3" span="8" :data="products">
						<ul>
							<li class="item" v-for="(item, name, index) in product_list2">
								<span>
								<img :src="item.bannerUrl" />
								<span><p class="title">{{item.name}}</p><p class="small_bh">编号：{{item.id}}</p><p class="price">¥{{item.price}}</p></span>
								</span>
								<span style="width:100%; float: right;">
								<Button type="primary" shape="circle" @click="modal2 = true">删除</Button>
								</span>
							</li>
						</ul>
					</TabPane>

				</Tabs>

			</div>
		</Content>
	</div>
</template>
<script>
	import { brandgetList, branddelete } from '../../api/api';
	export default {
		name: 'sbgl',
		data() {
			return {
				modal2: false,
				products: [],
				product_list: [],
				product_list1: [],
				product_list2: []

			}
		},
		components: {

		},
		methods: {
			onload() {
				if(sessionStorage.customerId == null) {
					this.$router.push({
						path: '/login'
					})
				}
			},
			//获取产品列表
			getProducts() {  
				let para = { 
					customerId: sessionStorage.customerId,
					status:0,
					pageSize:20
				};
				this.listLoading = true;
				//NProgress.start();
				brandgetList(para).then((res) => {
					//console.log(res);
					this.total = res.data.result.total;
					this.product_list = res.data.result.result;
					//console.log(this.product_list);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			getProducts1() {  
				let para = { 
					customerId: sessionStorage.customerId,
					status:1,
					pageSize:20
				};
				this.listLoading = true;
				//NProgress.start();
				brandgetList(para).then((res) => {
					//console.log(res);
					this.total = res.data.result.total;
					this.product_list1 = res.data.result.result;
					//console.log(this.product_list);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			getProducts2() {  
				let para = { 
					customerId: sessionStorage.customerId,
					status:2,
					pageSize:20
				};
				this.listLoading = true;
				//NProgress.start();
				brandgetList(para).then((res) => {
					//console.log(res);
					this.total = res.data.result.total;
					this.product_list2 = res.data.result.result;
					//console.log(this.product_list);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			prev() {
				this.$router.push({
					path: '/my'
				})
			},
			sbfb() {
				this.$router.push({
					path: '/sbfb'
				})
			},
			sbbj(id,categoryFir,inlandFlag,areaCode,publicFlag,) {
				this.$router.push({
					path: '/sbbj',
					query:{
						id:id,
						categoryFir:categoryFir,
						inlandFlag:inlandFlag,
						areaCode:areaCode,
						publicFlag:publicFlag, 
					}
				})
			},
			ok(id) {
				let para = {
                    ids:id
				};
				branddelete(para).then((res) => {
					this.listLoading = false;
					this.getProducts();
					this.$Message.success('商标删除成功');
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
					this.$Message.success('商标删除失败');
				});
			},
			cancel() {
				this.$Message.warning('已取消删除');
			},
			fenx() {
				this.$Message.success({
					content: '分享成功',
					duration: 3
				});
			},
			
			onload() { 
				if(sessionStorage.customerId == null) {
					this.$router.push({
						path: '/login'
					})
				}
			},
		},
		props: {},
		mounted() {
			//this.page = Number(sessionStorage.pageIndex);
			this.getProducts(); 
			this.getProducts1(); 
			this.getProducts2(); 
			this.onload(); 
		}

	}
</script>

<style lang="less" scoped>
	.title_right {
		display: inline-block;
		float: right;
	}
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		ul {
			width: 100%;
			margin-top: -16px;
			.item {
				padding: 20px 0;
				display: block;
				border-bottom: 1px solid #F6F6F6;
				height: auto !important;
				min-height: 20px;
				width: 100%;
				overflow: hidden;
				&:last-child{
					border: none;
				}
				button {
					background: #F4F4F4;
					border: 1px solid #E5E5E5;
					border-radius: 23px;
					width: 96px;
					height: 33px;
					line-height: 21px;
					font-size: 16px;
					float: left;
					margin-top: 15px;
					color: #666;
					margin: 15px 6px 0px 6px;
				}
				button:hover {
					color: #FFFFFF;
					background: #2B2EF5;
					border: none;
				}
			}
			img {
				height: 78px;
				width: 130px;
				border-radius: 3px;
				display: flex;
				float: left;
				margin-right: 10px;
			}
			span {
				display: inline-block;
				float: left;
				font-size: 16px;
				color: #000000;
				text-align: left;
				.title {
					font-size: 16px;
					color: #000000;
					font-weight: bold;
				}
				.small_bh {
					font-size: 12px;
					color: #9B9B9B;
				}
				.price {
					font-size: 20px;
					color: #2B2EF5;
					font-weight: bold;
				}
			}
		}
	}
</style>


