<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>我的收藏</Header>
		<Content class="content6 top_p2">
			<div class="hm_product_num">

				<Tabs value="name1" style="width: 100%;">
					<TabPane label="商标类" name="name1" @click="getsbProducts()">
						<ul>
							<li class="item" @click='sb_details(item.id)' v-for="(item, name, index) in sb_list">
								<img :src="item.bannerUrl" />
								<span><p class="title">{{item.name}}</p><p class="small_bh">编号：{{item.id}}</p><p class="price">¥{{item.price}}</p></span>
							</li>
						</ul>
			</TabPane>

			<TabPane label="专利类" name="name2" style="width: 100%;" @click="getzlProducts()">
				<ul>
					<li class="item" @click='zl_details(item.id)' v-for="(item, name, index) in zl_list">
						<img :src="item.bannerUrl" />
						<span><p class="title">{{item.name}}</p><p class="small_bh">编号：{{item.id}}</p><p class="price">¥{{item.price}}</p></span>
					</li>
				</ul>
	</TabPane>
	</Tabs>
	</div>
	</Content>
	</div>
</template>

<script>
	import {
		Collectlist
	} from '../../api/api';
	export default {
		name: 'myxj',
		data() {
			return { 
				sb_list: [],
				zl_list: [],
			}
		},
		components: {},
		props: {},
		methods: {
			//获取商标列表
			getsbProducts() {
				let para = {
					customerId: sessionStorage.customerId,
					actionType:'2',
					contentType:'1'
				};
				this.listLoading = true;
				//NProgress.start();
				Collectlist(para).then((res) => {   
					//console.log(res);
					this.sb_list = res.data.result.result.brands; 
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			//获取专利列表
			getzlProducts() {
				let para = {  
					customerId: sessionStorage.customerId, 
					actionType:'2',
					contentType:'2'
				};
				this.listLoading = true;
				//NProgress.start();
				Collectlist(para).then((res) => {   
					this.zl_list = res.data.result.result.patents; 
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
				});
			},
		
			prev() {
				this.$router.go(-1)
			},
			sb_details(id) {
				this.$router.push({
					path: '/sb_details',
					query:{
						id:id
					}
				})
			},
			zl_details(id) {
				this.$router.push({
					path: '/zl_details',
					query:{
						id:id
					}
				})
			}

		},
		mounted() {
			//this.page = Number(sessionStorage.pageIndex);
			this.getsbProducts();
			this.getzlProducts();
		}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		display: flex;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;
		ul {
			margin-top: -10px;
			width: 100%;
			.item {
				padding: 20px 0;
				display: block;
				border-bottom: 1px solid #F6F6F6;
				height: 120px;
				width: 100%;
				&:last-child{
					border: none;
				}
			}
			img {
				height: 78px;
				width: 130px;
				border-radius: 3px;
				display: flex;
				float: left;
				margin-right: 10px;
			}
			span {
				display: inline-block;
				float: left;
				font-size: 16px;
				color: #000000;
				text-align: left;
				.title {
					font-size: 16px;
					color: #000000;
					font-weight: bold;
				}
				.small_bh {
					font-size: 12px;
					color: #9B9B9B;
				}
				.price {
					font-size: 20px;
					color: #2B2EF5;
					font-weight: bold;
				}
			}
		}
	}
</style>


