<template>
	<div class="layout" v-model="products">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>{{products.name}}</Header>
		<Content class="top_p2">
			<img :src="products.bannerUrl" class="img" />
			<div class="hm_product_num">
				<Row type="flex" :gutter="16" class="de_cont">
					<col span="12">
					<p>商标价格: <br/> {{products.price}}</p>
					</col>
					<col span="12">
					<p>服务费: <br/> {{servicePrice}}</p>
					</col>
					<col span="12">
					<p>商标归属区域: <br/>{{products.areaCode}}</p>
					</col>
					<col span="12">
					<p>编号: <br/> {{products.id}}</p>
					</col>
				</Row>

				<p class="line"></p>
				<Row type="flex" :gutter="16" class="de_cont">
					<col span="12">
					<p>初审公告期号: <br/> {{products.firstNoticeNo}}</p>
					</col>
					<col span="12">
					<p>注册公告期号: <br/> {{products.registerNoticeNo}}</p>
					</col>

					<col span="12">
					<p>初审公告日期: <br/> {{products.firstNoticeTime}}</p>
					</col>
					<col span="12">
					<p>注册公告日期: <br/> {{products.registerNoticeTime}}</p>
					</col>
					<col span="12">
					<p>商标类型: <br/> {{products.categorySec}}</p>
					</col>
					<col span="12">
					<p>后期指定日期: <br/> {{products.laterAppointTime}}</p>
					</col>
					<col span="12">
					<p>到期时间: <br/> {{products.expireTime}}</p>
					</col>

					<col span="12">
					<p>国际注册日期: <br/> {{products.internationRegisterTime}}</p>
					</col>
					<col span="12">
					<p>优先权日期: <br/> {{products.priorityTime}}</p>
					</col>

					<col span="12">
					<p>专用期限: <br/> {{products.deadlineyear}}</p>
					</col>
					<col span="12">
					<p>是否公用商标: <br/> {{products.publicFlag == 'Y' ? '是':'否'}}</p>
					</col>
					<Button type="primary" shape="circle">联系方式：{{products.customerMobile}}</Button>
				</Row>

				<p class="line"></p>
				<div class="de_cont2 bottom_p">
					<span class="title">商标转让流程</span>
					<span class="smal-title">方便快捷又高效</span>
					<span class="title2">1.挑选商标</span>
					<span>
					资深商标顾问将根据您的需求提供一对一服务，对您选中的商标进行信息核实，查询其是否有过侵权行为、是否存在近似商标等信息
				</span>
					<span class="title2">2.签订合同</span>
					<span>

					确认商标无误，签订转让合同，合同中会表明商标详细信息及转让费用，双方签字，转让协议正式生效。</span>
					<span class="title2">3.商标极速公证</span>
					<span>
					与商标转让方进行资料核对，59秒极速出公证书，59分钟完成交易。
				</span>
					<span class="title2">4.邮寄资料，上报过户</span>
					<span>
					购买方收到我司寄出的《商标证》、《公证书》和《独占使用授权书》后即可使用；并在《转让/移转申请/注册商标申请书》等相关资料后需签字或盖章回寄到我公司提交国家商标局办理商标过户手续。
				</span>
					<span class="title2">
						5.核发证明，转让完成</span>
					<span>
					商标局收到转让文件后1-3个月左右下发《商标转让受理通知书》,6-10个月左右我司收到商标局下发《核准商标转让证明》后寄给购买方，购买方收到《核准商标转让证明》后与我公司签订的《委托购买商标协议书》终止。
				</span>
				</div>
			</div>
		</Content>
		<buyservice v-if="products.status===0"></buyservice>
		<buyservice v-if="products.status===null"></buyservice>
		<Modal v-model="modal12" title="服务评分" @on-ok="ok" @on-cancel="cancel" v-if="products.status===0">
			<Row>
				<Col span="12">
				<Rate show-text v-model="valueText" />
				</Col>
			</Row>
		</Modal>
	</div>
</template>

<script>
	import buyservice from './../public/buyservice.vue';
	import {
		brandgetById,
	} from '../../api/api';
	export default {
		name: 'sb_details',
		data() {
			return {
				products: [],
				modal12: false,
				valueText: 0,
				servicePrice:sessionStorage.brand_fee,
			}
		},
		components: {
			buyservice
		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			//获取产品列表
			getProducts() {
				this.listLoading = true;
				//NProgress.start(); 
				let applyType = this.$route.query.applyType;
				let para = {
					id: this.$route.query.id,
				};
				brandgetById(para).then((res) => {
					//console.log(res);
					this.products = res.data.result; 
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			ok() {
				this.$Message.info('Clicked ok');
			},
			cancel() {
				this.$Message.info('Clicked cancel');
			}
		},
		props: {},
		mounted() {
			this.getProducts();
		}
	}
</script>

<style lang="less" scoped>
	.img {
		width: 100%;
		height: 200px;
		display: block;
	}
	
	.top {
		margin-top: 200px;
	}
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		.de_cont {
			padding: 20px;
		}
		span {
			display: block;
			text-align: left;
		}
		p {
			font-size: 15px;
			color: #000000;
			line-height: 22px;
			width: 50%;
			text-align: left;
			padding: 7px;
		}
		.line {
			width: 100%;
			background-color: #D7D7D7;
			height: 1px;
			padding: 0;
		}
		.de_cont2 {
			padding: 20px;
			.title {
				font-size: 28px;
				color: #000000;
				font-weight: bold;
				height: 45px;
				line-height: 45px;
				text-align: center;
			}
			.smal-title {
				font-size: 13px;
				color: #9B9B9B;
				text-align: center;
			}
			.title2 {
				font-size: 18px;
				color: #2B2EF5;
				line-height: 18px;
				font-weight: bold;
				margin-top: 25px;
				margin-bottom: 8px;
			}
			span {
				font-size: 14px;
				color: #000000;
				line-height: 20px;
			}
		}
	}
</style>


