<template>
	<div class="layout">
		<Header class="header">登录</Header>
		<div class="login_logo  top_p">
			<img src="../../assets/login_logo.png" />
		</div>
		<div class="content6">
			<Form ref="formCustom" :data="formCustom" :model="formCustom" :rules="ruleCustom">
				<FormItem prop="loginName">
					<Input class="suk" v-model="formCustom.loginName" placeholder="请输入手机号码/邮箱/用户名">
					<Icon type="ios-contact-outline" slot="prefix" />
					</Input>
				</FormItem>
				<FormItem prop="passwd">
					<Input class="suk" type="password" v-model="formCustom.passwd" placeholder="请输入密码">
					<Icon type="ios-lock-outline" slot="prefix" />
					</Input>
				</FormItem>
				<FormItem>
					<Button type="primary" shape="circle" @click="handleSubmit('formCustom')">登录</Button>
					<p class="but_left" @click="wjmm">忘记密码?</p>
					<p class="but_right" @click="zhuc">注册</p>
				</FormItem>
			</Form>
		</div>
		<Footer class="footer">
			<Row type="flex">
				<col span="6">
				<router-link to="/"><img src="../../assets/icon_1.png" /><br />首页</router-link>
				</col>
				<col span="6">
				<router-link to="product"><img src="../../assets/icon_2.png" /><br />产品</router-link>
				</col>
				<col span="6">
				<router-link to="service"><img src="../../assets/icon_3.png" /><br />分类</router-link>
				</col>
				<col span="6">
				<a @click="myvip" class="active"><img src="../../assets/icon_4_hover.png" /><br />我的</a>
				</col>
			</Row>
		</Footer>
	</div>
</template>

<script>
	import { getUserlogin, getcustomergetById } from '../../api/api';
	export default {
		name: 'home',
		data() {
			return {
				formCustom: {
					passwd: '',
					loginName: ''
				},
				ruleCustom: {
					passwd: [{
						required: true,
						message: '密码不能为空',
						trigger: 'blur'
					}],
					loginName: [{
						required: true,
						message: '请输入手机号码/邮箱/用户名',
						trigger: 'blur'
					}]
				}
			}
		},
		components: {},
		methods: {
			handleSubmit(name) {
				this.$refs[name].validate((valid) => {
					if(valid) {
						let para = {
							loginName: this.formCustom.loginName,
							password: this.formCustom.passwd,
						};
						getUserlogin(para).then((res) => {
							if(res.data.code == 0) {
								this.$Message.success('登录成功!');
								sessionStorage.loginName = this.formCustom.loginName;
								sessionStorage.password = this.formCustom.passwd;
								sessionStorage.customerId = res.data.result.customerId;
								this.$router.push({
									path: '/my',
								})
							} else {
								this.$Message.warning(res.data.message);
							}
						});
					} else {
						this.$Message.error('输入有误');
					}
				})
			},
			wjmm() {
				this.$router.push({
					path: '/wjmm'
				})
			},
			zhuc() {
				this.$router.push({
					path: '/zhuc'
				})
			},

			myvip() {
				let loginName = sessionStorage.loginName
				if(loginName != null) {
					this.$router.push({
						path: '/my'
					})
				} else {
					this.$router.push({
						path: '/login'
					})
				}
			},
		},
		props: {},
		computed: {},
	}
</script>

<style lang="less" scoped>
	.login_logo {
		height: 180px;
		width: 100%;
		line-height: 240px;
		display: block;
		text-align: center;
		background-color: #2B2EF5;
		img {
			width: 112px;
			height: 96px;
			display: inline-block;
		}
	}
	
	.content6 {
		padding: 10px 20px;
		background-color: #FFFFFF;
		.suk {
			margin: 15px 0 -10px 0;
			display: block;
			background-color: #F6F6F6;
			border-radius: 50px;
		}
		.but_left {
			float: left;
			font-size: 14px;
		}
		.but_right {
			float: right;
			font-size: 14px;
		}
	}
</style>


