<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>发布求购
		</Header>
		<Content class="content6 top_p">
			<div class="hm_product_num">
				<Tabs value="name1">
					<TabPane label="商标求购发布" name="name1">
						<Form ref="formCustom" :data="formCustom" :model="formCustom" :rules="ruleCustom" class="big_h" :label-width="130">
							<FormItem class="my_list" prop="title" label="标题">
								<input v-model="formCustom.title" placeholder="请输入商标求购标题（20字以内）" />
							</FormItem>
							<FormItem class="my_list" prop="budget" label="预算">
								<input v-model="formCustom.budget" placeholder="请输入预算" />
							</FormItem>
							<FormItem class="my_list" prop="jsonStr" label="商标用途">
								<input v-model="formCustom.jsonStr" type="textarea" placeholder="请输入商标用途" />
							</FormItem>
							<Button type="primary" shape="circle" @click="handleSubmit('formCustom')">立即发布</Button>
						</Form>
					</TabPane>
					<TabPane label="专利求购发布" name="name2" span="8">
						<Form ref="formCustom" :data="formCustom" :model="formCustom" :rules="ruleCustom" class="big_h" :label-width="130">
							<FormItem class="my_list" prop="title" label="标题">
								<input v-model="formCustom.title" placeholder="请输入专利求购标题（20字以内）" />
							</FormItem>
							<FormItem class="my_list" prop="budget" label="预算">
								<input v-model="formCustom.budget" placeholder="请输入预算" />
							</FormItem>
							<FormItem class="my_list" prop="jsonStr" label="专利用途">
								<input v-model="formCustom.jsonStr" type="textarea" placeholder="请输入专利用途" />
							</FormItem>
							<Button type="primary" shape="circle" @click="handleSubmit2('formCustom')">立即发布</Button>
						</Form>
					</TabPane>

				</Tabs>

			</div>
		</Content>
	</div>
</template>
<script>
	import { Collectcreate } from '../../api/api';
	export default {
		name: 'fbqg',
		data() {
			return {
				formCustom: {
					title: '',
					budget: '',
					jsonStr: '',
				},
				ruleCustom: {
					title: [{
						required: true,
						message: ' ',
						trigger: 'blur'
					}],
					budget: [{
						required: true,
						message: ' ',
						trigger: 'blur'
					}],

					jsonStr: [{
						required: true,
						message: ' ',
						trigger: 'blur'
					}]
				},
			}
		},
		components: {

		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			handleSubmit(name) {
				this.$refs[name].validate((valid) => {
					if(valid) {
						let para = {
							title: this.formCustom.title,
							budget: this.formCustom.budget,
							jsonStr: this.formCustom.jsonStr,
							customerId: sessionStorage.customerId,
							customerMobile: sessionStorage.mobile,
							actionType: '1',
							contentType: '1',
						};
						Collectcreate(para).then((res) => {
							//console.log(res.data)
							if(res.data.code == 0) {
								this.$Message.success('发布成功!');
								this.$router.push({
									path: '/myqg'
								})
							} else {
								this.$Message.warning(res.data.message);
							}
						});
					} else {
						this.$Message.error('输入信息有误');
					}
				})
			},

			handleSubmit2(name) {
				this.$refs[name].validate((valid) => {
					if(valid) {
						let para = {
							title: this.formCustom.title,
							budget: this.formCustom.budget,
							jsonStr: this.formCustom.jsonStr,
							customerId: sessionStorage.customerId,
							customerMobile: sessionStorage.mobile,
							actionType: '1',
							contentType: '2',
						};
						Collectcreate(para).then((res) => {
							console.log(res.data)
							if(res.data.code == 0) {
								this.$Message.success('发布成功!');
								this.$router.push({
									path: '/myqg'
								})
							} else {
								this.$Message.warning(res.data.message);
							}
						});
					} else {
						this.$Message.error('输入信息有误');
					}
				})
			},

			zlfb() {
				this.$router.push({
					path: '/zlfb'
				})
			},
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.title_right {
		display: inline-block;
		float: right;
	}
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		.big_h {
			margin-top: -15px;
			margin-bottom: 30px;
			.my_list {
				border-bottom: 1px solid #E6E6E6;
				display: block;
				font-size: 16px;
				-webkit-box-pack: justify;
				-ms-flex-pack: justify;
				justify-content: space-between;
				padding-top: 10px;
				margin-top: -20px;
				line-height: 50px;
				.left {
					align-content: flex-start;
					.input_class {
						background-color: #FFFFFF;
						border: none;
					}
					.title {
						border-radius: 0 0 6px 6px;
						bottom: 0px;
						color: #000000;
						margin: 0;
						float: left;
						display: inline-block;
						width: 100px;
						text-align: left;
						align-content: flex-start;
					}
				}
				input {
					border: none;
					outline: none;
					font-size: 16px;
					display: inline-block;
					float: right;
					height: 58px;
					width: 260px;
					text-align: right;
					margin-top: -6px;
				}
			}
		}
	}
</style>


