			}, 
			handleRemove(file) {
				const fileList = this.$refs.upload.fileList;
				this.$refs.upload.fileList.splice(fileList.indexOf(file), 1);
			},
			handleSuccess(res, file) {
				file.bannerUrl = 'http://58.87.66.77:80' + res.result;
				this.formCustom.bannerUrl = 'http://58.87.66.77:80' + res.result;
			},
			handleFormatError(file) {
				this.$Notice.warning({
					title: '文件格式不正确',
					desc: '文件格式的' + file.name + '是否正确，请选择jpg或png.'
				});
			},
			handleMaxSize(file) {
				this.$Notice.warning({
					title: '超过文件大小限制',
					desc: 'File  ' + file.name + '太大了，不能超过2M.'
				});
			},
			handleBeforeUpload() {
				const check = this.uploadList.length < 1;
				if(!check) {
					this.$Notice.warning({
						title: '最多可以上传一张照片.'
					});
				}
				return check;
			},

			prev() {
				this.$router.go(-1)
			},
			initFormatter() {
				Date.prototype.Format = function(fmt) { //
					let o = {
						"M+": this.getMonth() + 1, //月份
						"d+": this.getDate(), //日
						"h+": this.getHours(), //小时
						"m+": this.getMinutes(), //分
						"s+": this.getSeconds(), //秒
						"q+": Math.floor((this.getMonth() + 3) / 3), //季度
						"S": this.getMilliseconds() //毫秒
					};
					if(/(y+)/.test(fmt))
						fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
					for(var k in o)
						if(new RegExp("(" + k + ")").test(fmt))
							fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
					return fmt;
				}
			},
			handleSubmit(name) {
				this.$refs[name].validate((valid) => {
					if(valid) {
						let laterAppointTime = this.formCustom.laterAppointTime;
						laterAppointTime = new Date(laterAppointTime).Format('yyyy-MM-dd');
						let internationRegisterTime = this.formCustom.internationRegisterTime;
						internationRegisterTime = new Date(internationRegisterTime).Format('yyyy-MM-dd');
						let priorityTime = this.formCustom.priorityTime;
						priorityTime = new Date(priorityTime).Format('yyyy-MM-dd');
						let expireTime = this.formCustom.expireTime;
						expireTime = new Date(expireTime).Format('yyyy-MM-dd');
							let para = {
								bannerUrl: this.formCustom.bannerUrl,
								name: this.formCustom.name,
								price: this.formCustom.price,
								areaCode: this.formCustom.areaCode,
								inlandFlag: this.formCustom.inlandFlag,
								categoryFir: this.formCustom.categoryFir,
								applyRange: this.formCustom.applyRange,
								firstNoticeNo: this.formCustom.firstNoticeNo,
								laterAppointTime: laterAppointTime,
								expireTime: expireTime,
								priorityTime: priorityTime,
								internationRegisterTime: internationRegisterTime,
								deadlineyear: this.formCustom.deadlineyear,
								publicFlag: this.formCustom.publicFlag,
								customerId: sessionStorage.customerId,
								customerMobile: sessionStorage.mobile,
							};
							brandcreate(para).then((res) => {
								//console.log(res.data)
								if(res.data.code == 0) {
									this.$Message.success('发布成功!');
									this.$router.push({
										path: '/sbgl'
									})
								} else {
									this.$Message.warning(res.data.message);
								}
							}); 
					} else {
						this.$Message.error('输入信息有误');
					}
				})
			}
