		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		.big_h {
			margin-bottom: 30px;
			.my_list {
				border-bottom: 1px solid #E6E6E6;
				display: block;
				font-size: 16px;
				-webkit-box-pack: justify;
				-ms-flex-pack: justify;
				justify-content: space-between;
				padding-top: 10px;
				margin-top: -20px;
				line-height: 50px;
				.img_box {
					width: 335px;
					height: 126px;
					position: absolute; 
					left: 0;
					top: 0;
				}
				.left {
					align-content: flex-start;
					.input_class {
						background-color: #FFFFFF;
						border: none;
					}
					.title {
						border-radius: 0 0 6px 6px;
						bottom: 0px;
						color: #000000;
						margin: 0;
						font-size: 16px;
						float: left;
						display: inline-block;
						width: 100px;
						text-align: left;
						align-content: flex-start;
					}
				}
				input {
					border: none;
					outline: none;
					font-size: 16px;
					display: inline-block;
					width: 230px;
					text-align: left;
				}
			}
		}
		.line {
			width: 100%;
			background-color: #D7D7D7;
			margin-bottom: 20px;
			height: 1px;
			padding: 0;
		}
	}
</style>


