<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>{{title}}</Header>
		<Content class="content2 top_p">
			<div class="vip_box">
				发布商标
			</div>
			<div class="vip_box">
				发布专利
			</div>
		</Content> 
	</div>
</template>
<script>
	export default {
		name: 'fbfl',
		data() {
			return {
				src: require('../../assets/deta_img.png'),
				price: '¥6000',
				title: '发布商品', 
			}
		},
		components: {

		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			sjvip() {
				this.$router.push({
					path: '/sjvip'
				})
			},
			sjvip() {
				this.$router.push({
					path: '/sjvip'
				})
			},
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.vip_box {
		height: 162px;
		line-height: 162px;
		width: 382px;
		display: block;
		margin: auto;
		background-color: #2C3E50;
		border-radius: 6px;
		color: #FFFFFF;
		font-size:32px;
		font-weight: bold;
	}
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		.big_title {
			font-size: 18px;
			color: #333333;
			line-height: 50px;
			vertical-align: middle;
		}
		.my_list {
			border-radius: 0 0 6px 6px;
			font-size: 16px;
			display: block;
			color: #000000;
			margin: 0;
			width: 100%;
			background: url(../../assets/vip_bg.png) 100% no-repeat;
			background-position: 0px 0px;
			height: 45px;
			text-align: left;
			line-height: 45px;
			text-indent: 16px;
			margin-bottom: 15px;
		}
	}
</style>


