<template>
	<div class="layout">
		<Header class="header2">
			<Modal v-model="modal1" @on-ok="ok" @on-cancel="cancel">
				<p class="tkwz">是否退出订单支付？</p>
			</Modal> 支付订单</Header>
		<Content class="top_p">
			<div class="hm_product_num">
				<Row type="flex" :gutter="16" class="de_cont">
					<col span="24">
					<span class="erwem"><img src="../../assets/ewmpng.png" />
					    <p class="tswz_title">
					    	扫描此二维码转账成功后，点击完成支付
						<br/>
						客服将与您联系，确认付款信息
						<br/>
						如未付款，请勿点击完成支付
					    </p>
					</span>
					</col>
					<col span="24">
					<p class="small_title">支付金额</p>
					<p v-if="vipFlag==='Y'" class="monery">{{price*0.9}}</p>
					<p v-if="vipFlag==='N'" class="monery">{{price}}</p>
					<p class="line"></p>
					<div v-if="vipFlag==='Y'">
					<p class="yhq_title">会员折扣券</p>
					<span class="box1">VIP会员服务费{{vipzk}}折（会员有效期内有效）</span>
					</div>
					<div v-else="vipFlag==='N'">
					<p class="yhq_title">您还不是vip会员</p>
					<span class="box2">暂无优惠券～</span>
					</div>
					</col>
					<col span="24">
					<Button type="text" shape="circle" @click="modal1 = true">退出支付</Button>
					<Button style="margin-top: -2px;" type="primary" shape="circle" @click="wanc">完成支付</Button>
					</col>
				</Row>
			</div>
		</Content>
	</div>
</template>

<script>
	import { applydoApply } from '../../api/api';
	export default {
		name: 'zhifuding',
		data() {
			return {
				price: this.$route.query.price,
				modal1: false,
				vipFlag: sessionStorage.vipFlag,
				vipzk: sessionStorage.vipzk
			}
		},
		components: {},
		methods: {
			onload() { 
				//console.log(sessionStorage.customerId) 
				if(sessionStorage.customerId == null) {
					this.$router.push({
						path: '/login'
					})
				}
			},
			prev() {
				this.$router.go(-1)
			},
			change(data) {},
			wanc() {
				if(sessionStorage.customerId == null) {
					this.$router.push({
						path: '/login'
					})
				} else {
					let para = {
						status:1,
						applyType: this.$route.query.applyType,
						customerId: sessionStorage.customerId,
						targetId: this.$route.query.id,
						amount: this.$route.query.price,
						description:this.$route.query.name,
						sallerMobile:sessionStorage.sallerMobile,
					};
					applydoApply(para).then((res) => {
						if(res.data.code == 0) {
							this.$Message.success('已收到订单支付信息');
							this.$router.push({
								path: '/daish'
							})
						} else {
							this.$Message.warning(res.data.message);
						}
					});
				}
			},
			ok() {
				if(sessionStorage.customerId == null) {
					this.$router.push({
						path: '/login'
					})
				} else {
					let para = {
						status:0,
						applyType: this.$route.query.applyType,
						customerId: sessionStorage.customerId,
						targetId: this.$route.query.id,
						amount: this.$route.query.amount,
					};
					applydoApply(para).then((res) => {
						console.log(res.data)
						if(res.data.code == 0) {
							this.$Message.warning('已取消支付');
							this.$router.go(-1)
						} else {
							this.$Message.warning(res.data.message);
						}
					});
				}
				},
				cancel() {
					this.$Message.success('请继续支付～');
				}
			},
			props: {},
		mounted() {
			this.onload(); 
		},
		}
</script>

<style lang="less" scoped>
	.tkwz {
		font-size: 18px;
		color: #000000;
	}
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		padding: 0 20px;
		overflow: hidden;
		.erwem {
			width: 100%;
			display: block;
			text-align: center;
			margin: 30px auto 20px auto;
		}
		.small_title {
			display: block;
			width: 100%;
			font-size: 16px;
			font-weight: bold;
			color: #333333;
		}
		.tswz_title {
			display: block;
			width: 100%;
			font-size: 15px;
			font-weight: bold;
			color: #ff6c00;
		}
		.monery {
			display: block;
			width: 100%;
			font-size: 36px;
			font-weight: bold;
			color: #2B2EF5;
		}
		.line {
			width: 100%;
			background-color: #D7D7D7;
			height: 1px;
			padding: 0;
			margin: 10px 0;
		}
		div{
			width: 100%;
					.yhq_title {
			font-size: 16px;
			color: #000000;
			width: 100%;
			font-weight: bold;
			text-align: center;
			display: block;
			margin-bottom: 10px;
		}
		.box1 {
			width: 100%;
			margin: 3px 0 15px 0;
			height: 55px;
			line-height: 55px;
			border-radius: 30px;
			font-size: 16px;
			color: #312300;
			text-align: center;
			font-weight: bold;
			background-color: #f0edd1;
			display: block;
		}
		
		.box2 {
			width: 100%;
			margin: 3px 0 15px 0;
			height: 55px;
			line-height: 55px;
			border-radius: 30px;
			font-size: 16px;
			color: #333333;
			text-align: center;
			font-weight: bold;
			background-color: #e6e6e6;
			display: block;
		}
		}
	}
</style>


