				position: absolute;
				height: 30px;
				line-height: 30px;
				bottom: 0px;
				font-size: 14px;
				z-index: 999;
				color: #FFFFFF;
				display: inline-block;
				width: 91%;
				background-color: rgba(0, 0, 0, 0.5);
			}
		}
		.line {
			width: 100%;
