<template>
	<div class="layout"  v-model="form" >
		<Header class="header"><span class="logo"><img src="../assets/logo.png"/></span></Header>
		<Carousel v-model="value1" class="top_p">
			<CarouselItem>
				<div class="demo-carousel">
					<img :src=form.oneimageUrl v-model="form.oneimageUrl" />
				</div>
			</CarouselItem>
			<CarouselItem>
				<div class="demo-carousel">
					<img :src=form.twoimageUrl v-model="form.twoimageUrl" />
				</div>
			</CarouselItem>
			<CarouselItem>
				<div class="demo-carousel">
					<img :src=form.threeimageUrl v-model="form.threeimageUrl" />
				</div>
			</CarouselItem>
			<CarouselItem>
				<div class="demo-carousel">
					<img :src=form.fourimageUrl v-model="form.fourimageUrl" />
				</div>
			</CarouselItem>
		</Carousel>

		<Content class="content">
			<div class="hm_product_num">
				<Row type="flex">
					<col span="4">
					<Button @click="product('北京市')">北京市</Button>
					</col>
					<col span="4">
					<Button @click="product('上海市')">上海市</Button>
					</col>
					<col span="4">
					<Button @click="product('广州市')">广州市</Button>
					</col>
					<col span="4">
					<Button @click="product('深圳市')">深圳市</Button>
					</col>
					<col span="4">
					<Button @click="product('河南省')">河南省</Button>
					</col>
				</Row>
				<Row type="flex">
					<col span="8">
					<p @click="product()"><img src="../assets/sbcs.png" />商标超市</p>
					</col>
					<col span="8">
					<p @click='service_details(zcsb.id,zcsb.name)'><img :src=zcsb.src />{{zcsb.name}}</p>
					</col>
					<col span="8">
					<p @click='qiugou'><img src="../assets/qgxx.png" />求购需求</p>
					</col>
				</Row>
			</div>
            <Phone></Phone> 
			<Row type="flex" :gutter="16">
				<col span="8">
				<span class="all_number">{{sbzstotal}}<br/>
				<p class="all_name">商标数</p></span>
				</col>
				<col span="8">
				<span class="all_number">{{zltotal}}<br/><p class="all_name">专利数</p></span>

				</col>
				<col span="8">
				<span class="all_number">{{ddtotal}}<br/>
				<p class="all_name">求购数</p></span>
				</col>
			</Row>
			<Row class="sever-img">
				<Col span="12"> <img src="../assets/gn.png" @click='service' /> </Col>
				<Col span="12"><img src="../assets/gw.png" @click='gj_service' /></Col>
			</Row>

			<Row type="flex" class="four_sever" :gutter="16">
				<col span="6">
				<p @click='service_details(sbcx.id,sbcx.name)'><img :src=sbcx.src /><br/>{{sbcx.name}}</p> 
				</col>

				<col span="6">
				<p @click="product()"><img src="../assets/zlcs.png" /><br/>专利超市</p>
				</col>

				<col span="6">
				<p @click='service_details(dbzc.id,dbzc.name)'><img :src=dbzc.src /><br/> {{dbzc.name}}</p>
				</col>

				<col span="6">
				<p @click='service_details(zlsq.id,zlsq.name)'><img :src=zlsq.src /><br/> {{zlsq.name}}</p>
				</col>
			</Row>
		</Content>
		<div class="img_banner" @click='service_details(company.id,company.name)'>
			<img src="../assets/banner-mid.png" />
		</div>
		<div class="sma_con">
			<Row type="flex" class="four_sever2" :gutter="16">
				<col span="6">
				<p @click='sb_know'><img src="../assets/sbknow.png" /><br/>商标知识</p>
				</col>

				<col span="6">
				<p @click='sb_ask'><img src="../assets/sbask.png" /><br/>商标问问</p>
				</col>

				<col span="6">
				<p @click='sb_law'><img src="../assets/sblaw.png" /><br/> 商标法律</p>
				</col>

				<col span="6">
				<p @click='com_new'><img src="../assets/connew.png" /><br/> 公司动态</p>
				</col>
			</Row>
		</div>
		<Content class="content2">
			<div class="hm_product_num">
				<Tabs value="name1">
					<TabPane label="优质商标" name="name1" span="8">
						<Row :gutter="16">
							<Col class="produce_list" span="12" v-for='(item, name, index) in neiproduct_list' :key="index">
							<div @click='sb_details(item.id)' style="margin-right: -8px;">
								<span class="title">{{item.name}}</span>
								<img :src="item.bannerUrl" />
							</div>
							</Col>
						</Row>
					</TabPane>
					<TabPane label="国际商标" name="name2">
						<Row :gutter="16">
							<Col class="produce_list" span="12" v-for='(item2, name, index) in waiproduct_list' :key="index">
							<div @click='service'>
								<span class="title">{{item2.name}}</span>
								<img :src="item2.bannerUrl" />
							</div>
							</Col>
						</Row>
					</TabPane>
					<TabPane label="优质名标" name="name3">
						<Row :gutter="16">
							<Col class="produce_list" span="12" v-for='(item3, name, index) in minproduct_list' :key="index">
							<div @click='sb_details'>
								<span class="title">{{item3.name}}</span>
								<img :src="item3.bannerUrl" />
							</div>
							</Col>
						</Row>
					</TabPane>
				</Tabs>
			</div>
		</Content>
		<div class="img_banner">
			<p class="phone_call">{{form.telephone}}</p>
			<img src="../assets/sever-banner.png" />
		</div>
		<Customer_service></Customer_service>
		<div class="img_banner bottom_p">
			<a target="_blank" href="http://wpa.qq.com/msgrd?v=3&uin=672081685&site=qq&menu=yes"><img src="../assets/bottom-banner.png" /></a>
		</div>
		</Content>
		<Footer class="footer">
			<Row type="flex">
				<col span="6">
				<router-link to="/" class="active"><img src="../assets/icon_1_hover.png" /><br />首页</router-link>
				</col>
				<col span="6">
				<router-link to='' to="product"><img src="../assets/icon_2.png" /><br />产品</router-link>
				</col>
				<col span="6">
				<router-link to="service"><img src="../assets/icon_3.png" /><br />分类</router-link>
				</col>
				<col span="6">
				<a @click="myvip"><img src="../assets/icon_4.png" /><br />我的</a>
				</col>
			</Row>
		</Footer>

	</div>

</template>

<script>
	import Phone from './public/phone.vue'; 
	import Customer_service from './public/Customer_service.vue';
	import { getUsersign, brandgetList, configgetList,patentgetList,servicegetList } from '../api/api';
	export default {
		name: 'home',
		data() {
			return {
				telephone: '',
				sbzstotal:'',
				zltotal:'',
				ddtotal:'',
				value1: 0,
				zcsb:[],
				zcsb:{
					id: '35',
					name: '注册商标',
					src: require('../assets/zcsb.png')
				},
				dbzc:[],
				dbzc:{
					id: '54',
					name: '担保注册',
					src: require('../assets/dbzc.png')
				},
				zlsq:[],
				zlsq:{
					id: '55',
					name: '专利申请',
					src: require('../assets/zlsq.png')
				},
				sbcx:[],
				sbcx:{
					id: '53',
					name: '商标查询',
					src: require('../assets/sbcx.png')
				},
				company:[],
				company:{
					id: '34',
					name: '公司介绍'
				},
				neiproduct_list: [],
				waiproduct_list: [],
				minproduct_list: [],
				form: [],
				form: {
					oneimageUrl: '',
					twoimageUrl: '',
					threeimageUrl: '',
					fourimageUrl: '',
				},
				oneimageUrl: '',
				twoimageUrl: '',
				threeimageUrl: '',
				fourimageUrl: '',
			}
		},
		methods: {
			created() {},
			//获取优质商标列表
			getneiProducts() {
				let para = {
					inlandFlag: 'Y',
				};
				this.listLoading = true;
				//NProgress.start();
				brandgetList(para).then((res) => {
					this.neiproduct_list = res.data.result.result;
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			configgetList() {
				let para = {};
				configgetList(para).then((res) => {
					this.data = res.data.result;
					let oneimageUrl = this.data[0].value;
					let twoimageUrl = this.data[1].value;
					let threeimageUrl = this.data[2].value;
					let fourimageUrl = this.data[3].value;
					let vipzk = this.data[4].value;
					let brand_fee = this.data[5].value;
					let patent_fee = this.data[6].value;
					let telephone = this.data[7].value;
					let form = {
						oneimageUrl: oneimageUrl,
						twoimageUrl: twoimageUrl,
						threeimageUrl: threeimageUrl,
						fourimageUrl: fourimageUrl,
						vipzk: vipzk,
						patent_fee: patent_fee,
						brand_fee: brand_fee,
						telephone: telephone,
					}
					this.form = form;
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
				});
			},
			//获取国际商标列表
			getwaiProducts() {
				let para = {
					inlandFlag: 'N',
				};
				this.listLoading = true;
				//NProgress.start();
				brandgetList(para).then((res) => {
					this.waiproduct_list = res.data.result.result;
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
				});
			},

			//获取总数列表列表
			getwaiProducts() {
				let para = {
				};
				this.listLoading = true;
				//NProgress.start();
				brandgetList(para).then((res) => {
					this.sbzstotal = res.data.result.total;
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
				});
			},
			//获取总数列表列表
			getsbProducts() {
				let para = {
				};
				this.listLoading = true;
				//NProgress.start();
				brandgetList(para).then((res) => {
					this.sbzstotal = res.data.result.total;
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
				});
			},
			//获取专利列表列表
			getzlProducts() {
				let para = {
				};
				this.listLoading = true;
				//NProgress.start();
				patentgetList(para).then((res) => {
					this.zltotal = res.data.result.total;
					this.listLoading = false; 
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
				});
			},
			//获取订单列表列表
			getddProducts() {
				let para = {
				};
				this.listLoading = true;
				//NProgress.start();
				servicegetList(para).then((res) => {
					this.ddtotal = res.data.result.total;
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
				});
			},
			//获取优质名标列表
			getminProducts() {
				let para = {
					vipFlag: 'Y',
				};
				this.listLoading = true;
				//NProgress.start();
				brandgetList(para).then((res) => {
					this.minproduct_list = res.data.result.result;
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
				});
			},
			product(site) {
				this.$router.push({
					path: '/product',
					query:{
						site:site
					}
				})
			},
			service_details(id,name) {
				this.$router.push({
					path: '/service_details',
					query:{
						id:id,
						name:name,
						applyType:'3'
					}
				})
			},
			qiugou() {
				this.$router.push({
					path: '/qiugou'
				})
			},
			gj_service() {
				this.$router.push({
					path: '/gj_service'
				})
			},
			service() {
				this.$router.push({
					path: '/service'
				})
			},
			sb_details(id) {
				this.$router.push({
					path: '/sb_details',
						query:{
							id:id
						}
				})
			},

			com_new() {
				this.$router.push({
					path: '/com_new'
				})
			},
			sb_ask() {
				this.$router.push({
					path: '/sb_ask'
				})
			},
			sb_know() {
				this.$router.push({
					path: '/sb_know'
				})
			},
			sb_law() {
				this.$router.push({
					path: '/sb_law'
				})
			},
			myvip() {
				let loginName = sessionStorage.loginName
				if(loginName != null) {
					this.$router.push({
						path: '/my'
					})
				} else {
					this.$router.push({
						path: '/login'
					})
				}
			},

		},
		props: {},
		components: {
			Phone,Customer_service
		},
		mounted() {
			this.created();
			this.getneiProducts();
			this.getwaiProducts();
			this.getsbProducts();
			this.getddProducts();
			this.getzlProducts();
			this.getminProducts();
			this.configgetList();
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
	.layout {
		.content {
			.hm_product_num {
				width: 100%;
				display: block;
				height: auto !important;
				min-height: 20px;
				overflow: hidden;
				p {
					font-size: 16px;
					color: #000000;
					text-align: center;
					width: 33%;
					margin-bottom: 20px;
					img {
						height: 76px;
						width: 76px;
						display: block;
						margin: 0 auto 5px auto;
					}
				}
				.center {
					margin: 0 13%;
				}
				Button {
					margin: 10px 5px 26px 0;
					float: left;
				}
			}
			.search {
				height: 45px;
				border: none;
				margin-top: 13px;
			}
			.all_number {
				font-size: 30px;
				color: #000000;
				font-weight: 500;
				text-align: center;
				line-height: 28px;
				display: block;
				width: 33%;
				margin: 26px 0;
				.all_name {
					font-size: 14px;
					color: #4A4A4A;
					text-align: center;
					display: block;
				}
			}
			.sever-img {
				display: flex;
				img {
					text-align: center;
					flex: 1;
					justify-content: center;
					width: 92%;
				}
			}
			.four_sever {
				margin: 32px 0 0 0;
				display: flex;
				font-size: 14px;
				color: #000000;
				text-align: center;
				p {
					justify-content: center;
					width: 25%;
					float: left;
					text-align: center;
				}
			}
		}
		.top_p {
			img {
				width: 100%;
				height: 200px;
			}
		}
		.sma_con {
			padding: 20px;
			background-color: #FFFFFF;
			margin-top: 10px;
			.four_sever2 {
				margin: 0 0 0 0;
				display: flex;
				font-size: 14px;
				color: #000000;
				text-align: center;
				p {
					justify-content: center;
					width: 25%;
					float: left;
					text-align: center;
				}
			}
		}
	}
	
	.img_banner {
		position: relative;
		display: block;
		margin-top: 10px;
		img {
			width: 100%;
			margin: 0;
			padding: 0;
			display: block;
		}
		.phone_call {
			font-size: 30px;
			font-weight: bold;
			color: #FFFFFF;
			z-index: 99;
			position: absolute;
			bottom: 5px;
			left: 120px;
		}
	}
	
	.produce_list {
		position: relative;
		overflow: hidden;
		height: 122px;
		margin-bottom: 15px;
		img {
			border-radius: 6px;
			width: 100%;
			height: 122px;
		}
		.title {
			border-radius: 0 0 6px 6px;
			position: absolute;
			height: 30px;
			line-height: 30px;
			bottom: 0px;
			font-size: 14px;
			z-index: 999;
			color: #FFFFFF;
			display: inline-block;
			width: 96%;
			background-color: rgba(0, 0, 0, 0.5);
		}
	}
</style>


