<template>
	<div class="layout">
		<div class="ser_banner">
			<p class="ser_header">中商服务</p>
			<p class="big_title">服务专业 省心省力</p>
			<p class="banner_p">多个合作伙伴 最快响应速度 最便捷服务通道</p>
		</div>
		<Content>
			<div class="hm_product_num bottom_p">
				<Row type="flex" class="four_sever" :gutter="16">
					<col span="6">
					<p @click='ser_deta(item.id,item.name)' v-for="(item, name, index) in service_list" :key="index"><img :src="item.src" /><br/>{{item.name}}</p>
					</col> 
				</Row>
				<Row class="sev_list">
					<Col span="12" v-for="(item, name, index) in service_list2" :key="index">
					<span  @click='ser_deta(item.id)'>{{item.name}}</span>
					</Col> 
				</Row>
				<Row type="flex" class="four_sever" :gutter="16">
					<col span="6">
					<p @click='ser_deta(item.id)' v-for="(item, name, index) in service_list3" :key="index"><img :src="item.src" /><br/>{{item.name}}</p>
					</col> 
				</Row>
			</div>
		</Content>
		<Footer class="footer">
			<Row type="flex">
				<col span="6">
				<router-link to="/"><img src="../../assets/icon_1.png" /><br />首页</router-link>
				</col>
				<col span="6">
				<router-link to="product"><img src="../../assets/icon_2.png" /><br />产品</router-link>
				</col>
				<col span="6">
				<router-link to="service" class="active"><img src="../../assets/icon_3_hover.png" /><br />分类</router-link>
				</col>
				<col span="6">
				<a @click="myvip"><img src="../../assets/icon_4.png" /><br />我的</a>
				</col>
			</Row>
		</Footer>
	</div>
</template>

<script> 
	export default {
		name: 'home',
		data() {
			return {
				service_list:[
				{
					id: '35',
					name: '注册商标',
					src: require('../../assets/sever_1.png')
				},
				{
					id: '37',
					name: '商标续费',
					src: require('../../assets/sever_2.png')
				},
				{
					id: '36',
					name: '商标变更',
					src: require('../../assets/sever_3.png')
				},
				{
					id: '38',
					name: '商标转让',
					src: require('../../assets/sever_4.png')
				},
				{
					id: '39',
					name: '商标撤回',
					src: require('../../assets/sever_5.png')
				},
				{
					id: '40',
					name: '专利服务',
					src: require('../../assets/sever_6.png')
				},
				{
					id: '41',
					name: '补注册证',
					src: require('../../assets/sever_7.png')
				},
				{
					id: '42',
					name: '不予册复',
					src: require('../../assets/sever_8.png')
				}],
				
				service_list2:[
				{
					id: '43',
					name: '商标变更异议申请', 
				},
				{
					id: '44',
					name: '撤销三年不使用', 
				},
				{
					id: '45',
					name: '不注册复审申请', 
				},
				{
					id: '46',
					name: '商标无效宣告申请', 
				},
				{
					id: '47',
					name: '撤销三年答辩', 
				},
				{
					id: '48',
					name: '商标无效宣告答辩', 
				}
				],
				service_list3:[
				{
					id: '49',
					name: '商标注销',
					src: require('../../assets/sever_9.png')
				},
				{
					id: '50',
					name: '异议答辩',
					src: require('../../assets/sever_10.png')
				},
				{
					id: '51',
					name: '驳回复审',
					src: require('../../assets/sever_11.png')
				},
				{
					id: '52',
					name: '其他服务',
					src: require('../../assets/sever_12.png')
				},
				]
			}
		},
		components: {
			 
		},
		methods: {
			ser_deta(id,name) {
				this.$router.push({
					path: '/service_details',
					query:{
						id:id,
						name:name,
						applyType:'0'
					}
				})
			},
			myvip() { 
				let loginName=sessionStorage.loginName 
				if(loginName != null){ 
				this.$router.push({
					path: '/my'
				})
				}else{ 
				this.$router.push({
					path: '/login'
				})
				}
			},
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.ser_banner {
		background-image: url(../../assets/ser_banner.png);
		height: 150px;
		width: 100%;
		display: block;
		.ser_header {
			font-size: 18px;
			width: 100%;
			color: #fff;
			font-weight: bold;
			padding: 0 20px;
			height: 60px;
			line-height: 60px;
			display: block;
			text-align: left;
		}
		.big_title {
			font-family: PingFang-SC-Bold;
			font-size: 26px;
			color: #FFFFFF;
		}
		.banner_p {
			font-family: PingFang-SC-Regular;
			font-size: 13px;
			color: #FFFFFF;
		}
	}
	
	.hm_product_num {
		width: 100%;
		padding:0 0 20px 0;
		display: block;
		height: auto !important;
		min-height: 510px;
		background-color: #FFFFFF; 
		overflow: hidden;
		.four_sever {
			margin: 0;
			padding: 0 20px;
			display: flex;
			font-size: 14px;
			color: #000000;
			text-align: center;
			p {
				justify-content: center;
				width: 25%;
				float: left;
				margin-top: 15px;
				text-align: center;
			}
		}
		.sev_list {
			margin: 15px 0 0px 5px;
			span {
				display: block;
				font-size: 16px;
				color: #FFFFFF; 
				line-height: 50px;
				height: 50px;
				background-image: url(../../assets/sev_bg2.png);
				background-repeat: no-repeat;
				background-size:100% 100%;
				margin: 0 5px 5px 0;
				text-align: left;
				text-indent: 35px;
				padding: 0;
			}
			.bg2 {
				display: block;
				font-size: 16px;
				color: #FFFFFF; 
				line-height: 50px;
				height: 50px;
				background-image: url(../../assets/sev_bg1.png);
				background-repeat: no-repeat;
				background-size:100% 100%;
				margin: 0 5px 5px 0;
				text-align: left;
				text-indent: 35px;
				padding: 0;
			}
		}
	}
</style>


