<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>找回密码</Header>
		<div class="content6 top_p">
			<Form ref="formCustom" :model="formCustom" :rules="ruleCustom">
				<FormItem prop="mail">
					<Input class="suk" v-model="formCustom.mail" placeholder="请输入邮箱/用户名/手机号码">
					<Icon type="ios-contact-outline" slot="prefix" />
					</Input>
				</FormItem>
				<FormItem>
					<Button type="primary" shape="circle" @click="handleSubmit('formCustom')">提交</Button>
				</FormItem>
			</Form>
		</div>
	</div>
</template>

<script>
	import { getUserfindPwd } from '../../api/api';
	export default {
		name: 'wjmm',
		data() {
			return {
				formCustom: {
					mail: ''
				},
				ruleCustom: {
					mail: [{
							required: true,
							message: '邮箱地址不能为空',
							trigger: 'blur'
						},
						{
							type: 'email',
							message: '输入正确格式的邮箱格式',
							trigger: 'blur'
						}
					]
				}
			}
		},
		components: {},
		props: {},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			handleSubmit(name) {
				this.$refs[name].validate((valid) => {
					let _this = this;
					if(valid) {
						let para = {
							loginName: this.formCustom.mail
						};
						getUserfindPwd(para).then((res) => {
							if(res.data.code == 0) {
								const customerId = res.data.result;
								this.$Message.success('验证成功');
								this.$router.push({
									path: '/xiugmm',
									query: {
										customerId
									}
								})
							} else {
								this.$Message.warning(res.data.message);
							}
						});
					} else {
						this.$Message.error('输入有误!');
					}
				})
			}
		},
	}
</script>

<style lang="less" scoped>
	.login_logo {
		height: 180px;
		width: 100%;
		line-height: 240px;
		display: block;
		text-align: center;
		background-color: #2B2EF5;
		img {
			width: 129px;
			height: 71px;
			display: inline-block;
		}
	}
	
	.content6 {
		padding: 10px 20px;
		background-color: #FFFFFF;
		.suk {
			margin: 15px 0 -10px 0;
			display: block;
			background-color: #F6F6F6;
			border-radius: 50px;
		}
	}
</style>


