<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click="prev">
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>等待验证</Header>
		<Content class=" top_p">
			<div class="hm_product_num">
				<Row type="flex" :gutter="16" class="de_cont">
					<col span="24">
					<p class="erwem"><img src="../../assets/succe.png" /></p>
					</col>
					<col span="24">
					<p class="small_title">验证信息已发至邮箱：{{email}}<br/> 请登录邮箱去验证
					</p>
					</col>
					<col span="24">
					<Button type="primary" shape="circle" @click="ok">去验证</Button>
					</col>
				</Row>
			</div>
		</Content>
	</div>
</template>

<script>
	export default {
		name: 'zhifuding',
		data() {
			return { 
				modal1: false,
				email: sessionStorage.mail,
			}
		},
		components: {},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			change(data) {},
			ok() {  
				window.location.href = 'https://mail.qq.com/cgi-bin/loginpage?t=fox_loginpage&sid=,2,zh_CN&r=db618582894c20894dfb8e391e9ab648',
				window.open(href, '_blank')
			},
			cancel() {
				this.$Message.warning('验证失败！');
			}
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.tkwz {
		font-size: 18px;
		color: #000000;
	}
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		padding: 0 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		.erwem {
			width: 100%;
			display: block;
			text-align: center;
			margin: 30px auto 20px auto;
		}
		.small_title {
			display: block;
			width: 100%;
			font-size: 16px;
			font-weight: bold;
			color: #333333;
		}
	}
</style>


