<template>
	<div class="layout">
		<Header class="header2"> 个人中心</Header>
		<Content class="content6 top_p">
			<div class="hm_product_num"> 
				<div class="per_top">
					<div class="per_avatar" :model="person">
						<img :src="person.headLogo" />
					</div>
					<div class="per_box" v-model="person">
						<span class="per_mess"><p class="per_title">{{person.loginName}}</p>
							<span v-if="person.vipFlag==='N'"><p @click="sjvip"><img src="../../assets/V.png"/>升级VIP></p><Icon type="ios-create-outline" @click="glzh" class="icon_right"/></span>
						<span v-else><p @click="sjvip" class="vip"><img src="../../assets/vip.png"/>VIP会员</p></span>
						</span>
						<div class="price" @click="pri_zh(person.balance)"><img src="../../assets/yue.png" />¥{{person.balance}}</div>
						</span>
				</div> 
				</div>
			</div>
		</Content>
		<Content class="content31 bottom_p">
			<div class="hm_product_num">
				<Tabs value="name1">
					<TabPane label="我是卖家" name="name1" span="8">
						<Row :gutter="16" class="big_h">
							<Col span="24">
							<div @click='sbgl' class="my_list">
								<span class="left">
									<img src="../../assets/sbicon.png" />
									<p class="title">商标管理 </p></span>
								<Icon type="ios-arrow-forward" class="go" />
							</div>
							</Col>
							<Col span="24">
							<div @click='zlgl' class="my_list">
								<span class="left">
									<img src="../../assets/zlicon.png" />
									<p class="title">专利管理</p></span>
								<Icon type="ios-arrow-forward" class="go" />
							</div>
							</Col> 
							</Col>
						</Row>
					</TabPane>

					<TabPane label="我是买家" name="name2" span="8">
						<Row :gutter="16" class="big_h">
							<Col span="24">
							<div @click='sb_order' class="my_list">
								<span class="left">
									<img src="../../assets/jiaoyi.png" />
									<p class="title">我的订单</p></span>
								<Icon type="ios-arrow-forward" class="go" />
							</div>
							</Col>
							<Col span="24">
							<div @click='myqg' class="my_list">
								<span class="left">
									<img src="../../assets/order1.png" />
									<p class="title">我的求购</p></span>
								<Icon type="ios-arrow-forward" class="go" />
							</div>
							</Col>
						</Row>
					</TabPane>
				</Tabs>
				<Row>
					<Col span="24">
					<div @click='myyhq' class="my_list">
						<span class="left">
									<img src="../../assets/order3.png" />
									<p class="title">我的优惠券</p></span>
						<Icon type="ios-arrow-forward" class="go" />
					</div>
					</Col>
					<Col span="24">
					<div @click='myfx' class="my_list">
						<span class="left">
									<img src="../../assets/scicon.png" />
									<p class="title">我的收藏</p></span>
						<Icon type="ios-arrow-forward" class="go" />
					</div>
					</Col>
					<Col span="24">
					<div @click='xgmm' class="my_list">
						<span class="left">
									<img src="../../assets/order4.png" />
									<p class="title">修改密码</p></span>
						<Icon type="ios-arrow-forward" class="go" />
					</div>
					</Col>
					<Col span="24">
					<div @click='yjfk' class="my_list">
						<span class="left">
									<img src="../../assets/order5.png" />
									<p class="title">意见反馈</p></span>
						<Icon type="ios-arrow-forward" class="go" />
					</div>
					</Col>
				</Row>
				<Button type="primary" shape="circle" @click="exit">退出</Button>
			</div>
		</Content>
		<Footer class="footer">
			<Row type="flex">
				<col span="6">
				<router-link to="/"><img src="../../assets/icon_1.png" /><br />首页</router-link>
				</col>
				<col span="6">
				<router-link to="product"><img src="../../assets/icon_2.png" /><br />产品</router-link>
				</col>
				<col span="6">
				<router-link to="service"><img src="../../assets/icon_3.png" /><br />分类</router-link>
				</col>
				<col span="6">
				<a @click="myvip" class="active"><img src="../../assets/icon_4_hover.png" /><br />我的</a>
				</col>
			</Row>
		</Footer>
	</div>
</template>
<script>
	import { getcustomerlist, getcustomergetById,configgetList } from '../../api/api';
	export default {
		name: 'my',
		data() {
			return { 
				person: {
					headLogo: require('../../assets/atavar.png'),
					loginName: '编辑个人资料',
					balance:'***',
					vipFlag:'N'
				},
			}
		},
		methods: {
			onload() {
				if(sessionStorage.customerId == null) {
					this.$router.push({
						path: '/login'
					})
				}
			},
			configList() {
				let para = {};
				configgetList(para).then((res) => {
					this.data = res.data.result;
					sessionStorage.vipzk = this.data[4].value;
					sessionStorage.brand_fee = this.data[5].value; 
					sessionStorage.patent_fee = this.data[6].value;  
					sessionStorage.telephone = this.data[7].value;  
				}).catch((err) => {
					//console.log(err);
				});
			},
			//获取用户信息
			getuser() {
				this.listLoading = true;
				//NProgress.start();
				let customerId =sessionStorage.customerId;
				getcustomergetById({customerId}).then((res) => {
					//console.log(res)
					this.person = res.data.result; 
					sessionStorage.vipFlag = this.person.vipFlag;  
					sessionStorage.mobile = this.person.mobile;  
					if(res.data.code == 2004) {
						this.$router.push({
							path: '/login'
						}) 
					} else if(this.person.loginName === null, this.person.headLogo === null) {
						this.person.loginName = '编辑个人资料';
						this.person.headLogo = require('../../assets/atavar.png');
					} else {
						this.person.loginName = this.person.loginName;
					}
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
				});
			},
			sjvip() {
				this.$router.push({
					path: '/sjvip'
				})
			},
			glzh() {
				this.$router.push({
					path: '/glzh'
				})
			},
			pri_zh(balance) {
				this.$router.push({
					path: '/pri_zh',
					query:{
						balance:balance
					}
				})
			},
			sbgl() {
				this.$router.push({
					path: '/sbgl',
				})
			},
			zlgl() {
				this.$router.push({
					path: '/zlgl'
				})
			},
			myjy() {
				this.$router.push({
					path: '/myjy'
				})
			},
			myzj() {
				this.$router.push({
					path: '/myzj'
				})
			},
			myfx() {
				this.$router.push({
					path: '/myfx'
				})
			},
			xgmm() {
				this.$router.push({
					path: '/xgmm'
				})
			},
			yjfk() {
				this.$router.push({
					path: '/yjfk'
				})
			},
			sb_order() {
				this.$router.push({
					path: '/sb_order'
				})
			},
			zl_order() {
				this.$router.push({
					path: '/zl_order'
				})
			},
			myqg() {
				this.$router.push({
					path: '/myqg'
				})
			},
			myyhq() {
				this.$router.push({
					path: '/myyhq'
				})
			},
			exit() {
				sessionStorage.clear();
				this.$Message.success('退出成功');
				this.$router.push({
					path: '/login'
				})
				//console.log(sessionStorage)
			},
			cancel() {
				this.$Message.warning('退出失败');
			},
			myvip() {
				let loginName = sessionStorage.loginName
				if(loginName != null) {
					this.$router.push({
						path: '/my'
					})
				} else {
					this.$router.push({
						path: '/login'
					})
				}
			},
		},
		props: {},
		computed: {},
		mounted() {
			//this.page = Number(sessionStorage.pageIndex);
			this.configList();
			this.getuser();
			this.onload();
		}
	}
</script>

<style lang="less" scoped>
	.bg_box {
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 152px;
		display: block;
		background-image: url('../../assets/ser_bg.png');
		font-size: 23px;
		color: #FFFFFF;
		text-align: center;
		line-height: 152px;
		font-weight: bolder;
	}
	
	.top {
		margin-top: 152px;
	}
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		.per_top {
			height: 53px;
			display: flex;
			align-content: space-between;
			.per_avatar {
				display: inline-block;
				width: 53px;
				height: 53px;
				border-radius: 53px;
				float: left;
				margin-right: 10px;
				background-color: #E6E6E6;
				img {
					width: 53px;
					height: 53px;
					border-radius: 53px;
					border: none;
				}
			}
			.icon_right {
				font-size: 26px;
				color: #666666;
			}
			.per_box {
				display: inline-block;
				float: left;
				width: 100%;
				.icon_right {
					float: right;
				}
				.per_mess {
					width: 100%;
					display: block;
					height: 30px;
					.vip {
						font-size: #FF5D00;
					}
					.per_title {
						font-size: 16px;
						color: #000000;
						text-align: left;
						font-weight: bold;
						display: inline;
						margin-right: 10px;
						float: left;
					}
					p {
						img {
							margin-right: 5px;
						}
						display: inline-block;
						float: left;
						font-size: 11px;
						color: #9B9B9B;
						letter-spacing: 0.26px;
						text-align: left;
					}
				}
				.price {
					width: 100%;
					display: block;
					height: 30px;
					font-weight: bold;
					img {
						width: 17px;
						height: 17px;
						margin-right: 7px;
						float: left;
					}
					font-size: 22px;
					color: #2B2EF5;
					text-align: left;
					line-height: 19px;
				}
			}
		}
		.big_h {
			margin-top: -16px;
		}
		.my_list {
			overflow: hidden;
			height: 60px;
			line-height: 60px;
			border-bottom: 1px solid #E6E6E6;
			display: flex;
			justify-content: space-between;
			.go {
				align-content: flex-end;
				font-size: 22px;
				vertical-align: middle;
				line-height: 60px;
				color: #8C8C8C;
			}
			.left {
				align-content: flex-start;
				img {
					border-radius: 6px;
					width: 29px;
					height: 29px;
					float: left;
					margin: 15px 8px 15px 0;
					align-content: flex-start;
				}
				.title {
					border-radius: 0 0 6px 6px;
					bottom: 0px;
					font-size: 16px;
					color: #000000;
					margin: 0;
					float: left;
					display: inline-block;
					width: 200px;
					text-align: left;
					align-content: flex-start;
				}
			}
		}
		.line {
			width: 100%;
			background-color: #E6E6E6;
			margin-bottom: 20px;
			height: 1px;
			padding: 0;
		}
	}
</style>


