<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>新用户注册</Header>
		<div class="login_logo  top_p">
			<img src="../../assets/login_logo.png" />
		</div>
		<div class="content6">
			<Form ref="formCustom" :data="formCustom" :model="formCustom" :rules="ruleCustom">
				<FormItem prop="mail">
					<Input class="suk" v-model="formCustom.mail" placeholder="请输入邮箱账号注册">
					<Icon type="ios-contact-outline" slot="prefix" />
					</Input>
				</FormItem>
				<FormItem prop="passwd">
					<Input class="suk" type="password" v-model="formCustom.passwd" placeholder="请设置密码">
					<Icon type="ios-lock-outline" slot="prefix" />
					</Input>
				</FormItem>
				<FormItem prop="passwdCheck">
					<Input class="suk" type="password" v-model="formCustom.passwdCheck" placeholder="请确认密码">
					<Icon type="ios-lock-outline" slot="prefix" />
					</Input>
				</FormItem>
				<FormItem>
					<Button type="primary" shape="circle" @click="handleSubmit('formCustom')">注册</Button>
				</FormItem>
			</Form>
		</div>
	</div>
</template>

<script>
	import { getUsersign } from '../../api/api';
	export default {
		name: 'zhuc',
		data() {

			const validatePass = (rule, value, callback) => {
				if(value === '') {
					callback(new Error('请设置密码'));
				} else {
					if(this.formCustom.passwdCheck !== '') {
						// 对第二个密码框单独验证
						this.$refs.formCustom.validateField('passwdCheck');
					}
					callback();
				}
			};
			const validatePassCheck = (rule, value, callback) => {
				if(value === '') {
					callback(new Error('请确认密码'));
				} else if(value !== this.formCustom.passwd) {
					callback(new Error('两次输入的密码不一致'));
				} else {
					callback();
				}
			};
			return {
				formCustom: {
					passwd: '',
					passwdCheck: '',
					mail: ''
				},
				ruleCustom: {
					passwd: [{
						validator: validatePass,
						min: 5,
						message: '密码长度不能小于6位',
						trigger: 'blur'
					}],
					passwdCheck: [{
						validator: validatePassCheck,
						min: 5,
						message: '密码长度不能小于6位',
						trigger: 'blur'
					}],
					mail: [{
						required: true,
						message: '不能为空',
						trigger: 'blur',
					}, {
						type: 'string',
						pattern: /^\w+@[a-zA-Z0-9]{2,10}(?:\.[a-z]{2,4}){1,3}$/,
						message: '请输入正确的邮箱地址',
						trigger: 'blur',
					}]
				}
			}
		},
		components: {},
		methods: {
			prev() {
				this.$router.push({
					path: '/login'
				})
			},

			handleSubmit(name) {
				this.$refs[name].validate((valid) => {
					let _this = this;
					if(valid) {
						let para = {
							email: this.formCustom.mail,
							password: this.formCustom.passwd
						};
						getUsersign(para).then((res) => {
							if(res.data.code == 0) {
								sessionStorage.mail = this.formCustom.mail;
								this.$Message.success('发送成功!');
								this.$router.push({
									path: '/daiyanz'
								})
							} else {
								this.$Message.warning(res.data.message);
							}
						});
					} else {
						this.$Message.error('输入有误!');
					}
				})
			},
		},
		props: {},
	}
</script>

<style lang="less" scoped>
	.login_logo {
		height: 180px;
		width: 100%;
		line-height: 240px;
		display: block;
		text-align: center;
		background-color: #2B2EF5;
		img {
			width: 129px;
			height: 90px;
			display: inline-block;
		}
	}
	
	.content6 {
		padding: 10px 20px;
		background-color: #FFFFFF;
		.suk {
			margin: 15px 0 -10px 0;
			display: block;
			background-color: #F6F6F6;
			border-radius: 50px;
		}
		.but_left {
			float: left;
			font-size: 14px;
		}
		.but_right {
			float: right;
			font-size: 14px;
		}
	}
</style>


