<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>{{categoryFir}}·{{categorySec}}·专利</Header>
		<Content class="content6 top_p2">
			<div class="hm_product_num">
				<ul>
					<li class="item" @click='sb_details(item.id,item.name,item.price)' v-for="(item, name, index) in product_list">
						<img :src="item.bannerUrl" />
						<span><p class="title">{{item.name}}</p><p class="small_bh">编号：{{item.id}}</p><p class="price">¥{{item.price}}</p></span>
					</li>
				</ul>
			</div>
		</Content>
	</div>
</template>

<script>
	import { patentgetList } from '../../api/api';
	export default {
		name: 'zl_list',
		data() {
			return { 
				type:'',
				product_list: [],
				categoryFir:this.$route.query.type,
				categorySec:this.$route.query.categorySec,
			}
		},
		components: {},
		props: {},
		methods: {
			//获取产品列表
			getProducts() { 
				let para = { 
				categoryFir:this.$route.query.categoryFir,
				categorySec:this.$route.query.categorySec,
				};
				this.listLoading = true;
				//NProgress.start();
				patentgetList(para).then((res) => { 
					this.product_list = res.data.result.result;
					// console.log(this.product_list);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					// console.log(err);
				});
			},
			prev() {
				this.$router.go(-1)
			},
			sb_details(id,price,name) {
				this.$router.push({
					path: '/zl_details',
					query:{
						id:id,
						name:name,
						price:price,
				        applyType:this.$route.query.applyType,
					}
				})
			}

		},
		mounted() {
			this.getProducts();
		}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		display: flex;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;
		ul {
			width: 100%;
			.item {
				padding: 20px 0;
				display: block;
				border-bottom: 1px solid #F6F6F6;
				height: 120px;
				width: 100%;
			}
			img {
				height: 78px;
				width: 130px;
				border-radius: 3px;
				display: flex;
				float: left;
				margin-right: 10px;
			}
			span {
				display: inline-block;
				float: left;
				font-size: 16px;
				color: #000000;
				text-align: left;
				.title {
					font-size: 16px;
					color: #000000;
					font-weight: bold;
				}
				.small_bh {
					font-size: 12px;
					color: #9B9B9B;
				}
				.price {
					font-size: 20px;
					color: #2B2EF5;
					font-weight: bold;
				}
			}
		}
	}
</style>


