<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>{{title}}</Header>
		<Content class="content6 top_p">
			<div class="vip_box" v-if="vipFlag==='Y'">
				<p class="num">1</p>
				所有交易服务费{{vipzk}}折优惠券
			</div>
			<div class="no-vip_box" v-else="vipFlag==='N'">
				您不是会员，暂无优惠券～
			</div> 
		</Content> 
	</div>
</template>
<script>
	export default {
		name: 'fbfl',
		data() {
			return { 
				title: '我的优惠券', 
				vipFlag: sessionStorage.vipFlag,
				vipzk: sessionStorage.vipzk
			}
		},
		components: {

		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.vip_box {
		height: 102px;
		line-height: 102px;
		width: 382px;
		display: block;
		margin: auto;
		background-color: #FF7900;
		border-radius: 6px;
		color: #FFFFFF;
		font-size:16px;
		font-weight: bold;
		margin-bottom: 15px;
		position: relative;
	.num{
		height: 26px;
		width: 26px;
		border-radius: 30px;
		font-size: 16px;
		text-align: center;
		line-height: 30px;
		color: #0000FF;
		background-color: #FFFFFF;
		position: absolute;
		right: 20px;
		top: 20px;
	}
	}
	
	.no-vip_box {
		height: 102px;
		line-height: 102px;
		width: 372px;
		display: block;
		margin: auto;
		background-color: #F6F6F6;
		border-radius: 6px;
		color: #333333;
		font-size:16px;
		font-weight: bold;
		margin-bottom: 15px;
		position: relative;
		}
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		.big_title {
			font-size: 18px;
			color: #333333;
			line-height: 50px;
			vertical-align: middle;
		}
		.my_list {
			border-radius: 0 0 6px 6px;
			font-size: 16px;
			display: block;
			color: #000000;
			margin: 0;
			width: 100%;
			background: url(../../assets/vip_bg.png) 100% no-repeat;
			background-position: 0px 0px;
			height: 45px;
			text-align: left;
			line-height: 45px;
			text-indent: 16px;
			margin-bottom: 15px;
		}
	}
</style>


