<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>
			求购需求</Header>
		<Content class="content6 top_p">
			<div class="hm_product_num">
				<Tabs value="name1">
					<TabPane label="商标类" name="name1" span="8">

						<ul>
							<li class="item" v-model="product_list" @click='sb_qg(item.id)' v-for="(item, name, index) in product_list">
								<p class="title">{{item.name}}</p>
								<span><p class="small_bh">编号：{{item.id}}</p>
									<p class="price">发布日期：{{item.createTime}}</p>
									</span>
							</li>
						</ul>

					</TabPane>
					<TabPane label="专利类" name="name2">
						<ul>
							<li class="item" v-model="product_zllist"  @click='zl_qg(item.id)' v-for="(item, name, index) in product_zllist">
								<p class="title">{{item.name}}</p>
								<span><p class="small_bh">浏览：{{item.id}}人</p>
									<p class="price">发布日期：{{item.createTime}}</p>
									</span>
							</li>
						</ul>

					</TabPane>
				</Tabs>
			</div>
		</Content>
	</div>
</template>

<script>
	import { Collectlist } from '../../api/api';
	export default {
		name: 'qiugou',
		data() {
			return {
				product_list:[],
				product_zllist:[],
			}
		},
		components: {

		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			sb_qg(id) {
				this.$router.push({
					path: '/sb_qg',
					query:{
						id:id,
				        actionType:'2',
				        contentType:'1',
					}
				})
			},
			zl_qg(id) {
				this.$router.push({
					path: '/zl_qg',
					query:{
						id:id,
				        actionType:'2',
				        contentType:'1',
					}
				})
			},//获取产品列表
			getProducts() { 
				let para = { 
				actionType:'2',
				contentType:'1',
				};
				this.listLoading = true;
				//NProgress.start();
				Collectlist(para).then((res) => { 
					this.product_list = res.data.result.result.brands;
					//console.log(this.product_list);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				}); 
				let prams = { 
				actionType:'2',
				contentType:'2',
				};
				this.listLoading = true;
				//NProgress.start();
				Collectlist(prams).then((res) => { 
					this.product_zllist = res.data.result.result.patents;
					//console.log(this.product_zllist);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
		
		},
		props: {},
		mounted() {
			this.getProducts();
		}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;
		li {
			flex: 2;
			font-size: 18px;
			color: #000000;
			width: 100%;
			line-height: 22px;
			text-align: left;
			padding: 20px 0;
			border-bottom: 1px solid #E6E6E6;
			span {
				width: 100%;
				display: block;
				width: 100%;
				display: flex;
				justify-content: space-between;
				line-height: 30px;
				font-size: 15px;
				color: #666666;
				.small_bh {
					text-align: left;
					flex: 1;
					align-items: left;
				}
				.price {
					text-align: right;
					flex: 1;
					align-items: right;
				}
			}
		}
	}
</style>


