<template>
	<div class="search phone">
		<a class="phonebox" href="tel:010-80720072"></a>
		<Input search enter-button="找客服" placeholder="输入手机号码，为您提供服务" />
	</div>
</template>

<script>
	export default {
		name: 'phone',
		data() {
			return {}
		},
		computed: {},
	}
</script>
<style lang="less" scoped>
	.phone {
		position: relative;
		.phonebox {
			height: 45px;
			width: 81px;
			display: block;
			right: 0;
			top: 0;
			position: absolute;
			z-index: 99;
		}
	}
</style>


