<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>我的专利订单 </Header>
		<Content class="content2 top_p2">
			<div class="hm_product_num">
				<Tabs value="name1">
					<TabPane label="未完成" name="name1">
						<ul>
							<li class="item" v-for="(item, name, index) in product_list">
								<img :src="item.img" @click='zl_details' />
								<span><p class="title">{{item.title}}</p><p class="small_bh">编号：{{item.bianhao}}</p><p class="price">¥{{item.price}}</p></span>
								<span style="float: right; margin-top: -50px;"><Button type="primary" shape="circle" @click="modal3 = true"> 取消订单</Button> 
								</span>
								<Modal v-model="modal3" @on-ok="ok" @on-cancel="cancel">
									<p class="tkwz">确认取消订单？</p>
								</Modal>
							</li>
						</ul>
					</TabPane>
					<TabPane label="已完成" name="name2" span="8">
						<ul>
							<li class="item" v-for="(item, name, index) in product_list">
								<img :src="item.img" @click='zl_details' />
								<span><p class="title">{{item.title}}</p><p class="small_bh">编号：{{item.bianhao}}</p><p class="price">¥{{item.price}}</p></span>
								<span style="float: right; margin-top: -50px;">
								<Button type="primary" shape="circle"  @click="modal2 = true">删除订单</Button>
								<Modal v-model="modal2" @on-ok="ok" @on-cancel="cancel">
				<p class="tkwz">确认删除订单？</p>
			</Modal>
								</span>
							</li>
						</ul>
					</TabPane>
					<TabPane label="已取消" name="name3" span="8">
						<ul>
							<li class="item" v-for="(item, name, index) in product_list">
								<img :src="item.img" @click='zl_details' />
								<span><p class="title">{{item.title}}</p><p class="small_bh">编号：{{item.bianhao}}</p><p class="price">¥{{item.price}}</p></span>
								<span style="float: right; margin-top: -50px;">
								<Button type="primary" shape="circle"  @click="modal2 = true">删除订单</Button>
								</span>
							</li>
						</ul>
					</TabPane>

				</Tabs>

			</div>
		</Content>
	</div>
</template>
<script>
	export default {
		name: 'zlgl',
		data() {
			return {
				modal2: false,
				modal3: false,
				product_list: [{
					img: require('../../assets/img_sblist.png'),
					title: '温碧泉护肤品商标',
					bianhao: '09887',
					price: 3000
				}, {
					img: require('../../assets/img_sblist.png'),
					title: '温碧泉护肤品商标',
					bianhao: '09887',
					price: 2000
				}, {
					img: require('../../assets/img_sblist.png'),
					title: '温碧泉护肤品商标',
					bianhao: '09887',
					price: 1200
				}, {
					img: require('../../assets/img_sblist.png'),
					title: '温碧泉护肤品商标',
					bianhao: '09887',
					price: 6600
				}, {
					img: require('../../assets/img_sblist.png'),
					title: '温碧泉护肤品商标',
					bianhao: '09887',
					price: 3000
				}, {
					img: require('../../assets/img_sblist.png'),
					title: '温碧泉护肤品商标',
					bianhao: '09887',
					price: 2600
				}]

			}
		},
		components: {

		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			zlfb() {
				this.$router.push({
					path: '/zlfb'
				})
			},
			zl_details() {
				this.$router.push({
					path: '/zl_details'
				})
			},
			ok() {
				this.$Message.success('操作成功');
			},
			cancel() {
				this.$Message.warning('已取消');
			},
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.title_right {
		display: inline-block;
		float: right;
	}
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		ul {
			width: 100%;
			margin-top: -16px;
			.item {
				padding: 20px 0;
				display: block;
				border-bottom: 1px solid #F6F6F6;
				height: auto !important;
				min-height: 20px;
				overflow: hidden;
				width: 100%;
				button {
					background: #F4F4F4;
					border: 1px solid #E5E5E5;
					border-radius: 23px;
					width: 96px;
					height: 33px;
					line-height: 21px;
					font-size: 16px;
					float: left;
					margin-top: 15px;
					color: #666;
					margin: 15px 6px 0px 6px;
				}
				button:hover {
					color: #FFFFFF;
					background: #2B2EF5;
					border: none;
				}
			}
			img {
				height: 78px;
				width: 130px;
				border-radius: 3px;
				display: flex;
				float: left;
				margin-right: 10px;
			}
			span {
				display: inline-block;
				float: left;
				font-size: 16px;
				color: #000000;
				text-align: left;
				.title {
					font-size: 16px;
					color: #000000;
					font-weight: bold;
				}
				.small_bh {
					font-size: 12px;
					color: #9B9B9B;
				}
				.price {
					font-size: 20px;
					color: #2B2EF5;
					font-weight: bold;
				}
			}
		}
	}
</style>


