<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>我的求购<p class="title_right" @click='fbqg'>去发布</p></Header>
		<Content class="content top_p2">
			<div class="hm_product_num">

				<Tabs value="name1" style="width: 100%;">
					<TabPane label="商标求购类" name="name1" span="8">
						<ul>
							<li class="item"  v-for="(item, name, index) in sb_list"> 
								<span @click='sb_qg(item.id,item.actionType,item.contentType)'><p class="title">{{item.title}}</p> <p class="price">¥{{item.budget}}</p> <p>{{item.jsonStr}}</p></span>
								<span>
								<Button type="primary" shape="circle" @click="ok(item.id)">删除</Button>
								</span>
							</li>
						</ul>
			</TabPane>

			<TabPane label="专利求购类" name="name2" span="8" style="width: 100%;">
				<ul>
					<li class="item" v-for="(item, name, index) in zl_list"> 
						<span  @click='zl_qg(item.id,item.actionType,item.contentType)'><p class="title">{{item.title}}</p> <p class="price">¥{{item.budget}}</p> <p>{{item.jsonStr}}</p></span>
						<span>
						<Button type="primary" shape="circle" @click="ok2(item.id)">删除</Button>
						</span>
					</li>
				</ul>
	</TabPane>
	</Tabs>
	</div>
	</Content>
	</div>
</template>

<script>
	import {
		Collectlist,Collectdelete
	} from '../../api/api';
	export default {
		name: 'myqg',
		data() {
			return {
				sb_list: [],
				zl_list: [],
			}
		},
		components: {},
		props: {},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			//获取商标列表
			getsbProducts() {
				let para = {
					customerId: sessionStorage.customerId,
					actionType:'1',
					contentType:'1'
				};
				this.listLoading = true;
				//NProgress.start();
				Collectlist(para).then((res) => {   
					this.sb_list = res.data.result.result; 
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			//获取专利列表
			getzlProducts() {
				let para = {  
					customerId: sessionStorage.customerId, 
					actionType:'1',
					contentType:'2'
				};
				this.listLoading = true;
				//NProgress.start();
				Collectlist(para).then((res) => {   
					this.zl_list = res.data.result.result;   
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
				});
			},
			fbqg() {
				this.$router.push({
					path: '/fbqg'
				})
			}, 
			ok(id) {
				let para = {
                    id:id,
					customerId: sessionStorage.customerId,
					actionType:'1',
					contentType:'1'
				};
				Collectdelete(para).then((res) => {
					this.listLoading = false;
					this.getProducts();
					this.$Message.success('商标删除成功');
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
					this.$Message.success('商标删除失败');
				});
			},
			
			ok2(id) {
				let para = {
                    id:id,
					customerId: sessionStorage.customerId, 
					actionType:'1',
					contentType:'2'
				};
				Collectdelete(para).then((res) => {
					this.listLoading = false;
					this.getProducts();
					this.$Message.success('商标删除成功');
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
					this.$Message.success('商标删除失败');
				});
			},
			sb_qg(id,actionType,contentType) {
				this.$router.push({
					path: '/sb_qg',
					query:{
						id:id,
						actionType:actionType,
						contentType:contentType,
					}
				})
			},
			zl_qg(id,actionType,contentType) {
				this.$router.push({
					path: '/zl_qg',
					query:{
						id:id,
						actionType:actionType,
						contentType:contentType,
					}
				})
			}

		},
		mounted() {
			//this.page = Number(sessionStorage.pageIndex);
			this.getsbProducts();
			this.getzlProducts();
		}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		display: flex;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;
		ul {
			width: 100%;
			margin-top: -10px;
			.item {
				padding: 20px 0;
				display: block;
				border-bottom: 1px solid #F6F6F6;
				min-height: 90px; 
				height: auto !important;
				overflow: hidden;
				width: 100%;
				&:last-child{
					border: none;
				}
				button {
					background: #F4F4F4;
					border: 1px solid #E5E5E5;
					border-radius: 23px;
					width: 96px;
					height: 33px;
					line-height: 21px;
					font-size: 16px;
					float: left;
					margin-top: 15px;
					color: #666;
					margin: 15px 6px 0px 6px;
				}
				button:hover {
					color: #FFFFFF;
					background: #2B2EF5;
					border: none;
				}
			} 
			span {
				display: block;
				width: 100%;
				float: left;
				font-size: 16px;
				color: #000000;
				text-align: left;
				.title {
					font-size: 16px;
					color: #000000;
					font-weight: bold;
				}
				.small_bh {
					font-size: 12px;
					color: #9B9B9B;
				}
				.price {
					font-size: 20px;
					color: #2B2EF5;
					font-weight: bold;
				}
			}
		}
	}
</style>


